<template>
	<div class="modalbox">
		<ul class="navlist">
			<li><a href="##">首页</a></li>
			<li><a href="##" v-text="goodsOne.goodsBigName"><span></span></a></li>
			<li><a href="##" v-text="goodsOne.goodsType"><span></span></a></li>
			<li v-text="goodsOne.goodsChName"></li>
		</ul>
		<div class="bigBox " >
			<div class="contentBox">
				<div class="left">
					<h3 class="chinese-title">{{goodsOne.goodsEnName}}</h3>
					<h3 class="english-title" v-text="goodsOne.goodsChName"></h3>
					<div class="sprite">
						<ul>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
						</ul>
						<a href="##">12条评论</a>
					</div>
					<dl>
						<dt>描述</dt>
						<dd v-text="goodsOne.goodsDes">
							
						</dd>
					</dl>					
				</div>
				<!-- 选项卡 -->
				<div class="center">
					<span class="promotion" v-text="goodsOne.goodsKind">刻字</span>
					<div class="big-picture">
						<img :src="goodsOne.goodsImg" alt="">
					</div>
					<a href="##" v-for="(i,index) in goodsOne.changeColor" :class="{'show':i.active}" @click='tabChose(i,index)'>
						<img :src="i.imgSrc" alt="">
					</a>
				</div>
				<div class="right">
					<p class="capacity">容量:</p>
					<select class="select-capacity" id="capacity" @change='changeColor'>
					  <option  v-for="(i,index) in goodsOne.changeColor" :value ="index" :class="{'show':i.active}">{{i.goodsColor}}</option>
					</select>
					<p class="number">
						数量:
						<select class="select-number">
						  <option value ="number1">01</option>
						  <option value ="number2">02</option>
						  <option value ="number3">03</option>
						  <option value ="number4">04</option>
						  <option value ="number5">05</option>
						</select>
						<span class="price" v-text="goodsOne.goodsPrice"></span>
					</p>
					<span class="purchase"  @click="addCart(goodsOne.goodsId)">立即购买</span>	
					<div class="jiathis_style">
			            <span class="share">
							<a class="wishlist" href="##">
								<span></span>心愿单
							</a>
						</span>	
					    <span class="jiathis_txt">分享：</span>
						<a class="jiathis_button_tsina" title="分享到微博"></a>
						<a class="jiathis_button_cqq" title="分享到QQ好友"></a>
						<a class="jiathis_button_weixin" title="分享到微信"></a>
						<a href="http://www.jiathis.com/share" class="jiathis jiathis_txt jtico jtico_jiathis" target="_blank"></a>
					</div>							
				</div>
			</div>
		</div>
	</div>
</template>
<script>
    import axios from 'axios';
	export default {
		data(){
			return {
				goodsOne:{}
			}
		},
		methods:{
			tabChose(item,index){
		　　　　this.goodsOne.goodsImg = item.imgSrc;  
				for(var i = 0;i<this.goodsOne.changeColor.length;i++){
		　　　　this.goodsOne.changeColor[i].active = false    
		 			}
				this.goodsOne.changeColor[index].active = true
			},
			changeColor(){
				var index =capacity.value;
				this.goodsOne.goodsImg = this.goodsOne.changeColor[index].imgSrc; 
				this.goodsOne.goodsPrice=this.goodsOne.changeColor[index].goodsPrice
			},
			addCart(goodsId){
				axios.post('/goods/addCart',{
					goodsId:goodsId
				}).then((res)=>{
					console.log(goodsId);
					if(res.status==0){
						
						alert("msg:"+res.msg);
					}else{
						alert("加入成功");
						window.location.reload();
						// alert("没有成功")
					}
				})
			}	
		},
		created(){
  			axios.get('/goods/goodsOne',{params:this.$route.query}).then((data)=>{
  				console.log(data.data);
  				this.goodsOne=data.data;
  			});
		 }
	}
</script>
<style scoped>
    @import url('../assets/css/normalize.css');
    ul li {
    	list-style: none;
    }
    .modalbox {
        width: 996px;
        margin: 0 auto;
    }
    .modalbox .navlist {
    	width: 100%;
    	height: 50px;
    	line-height: 50px;
    }
    .modalbox .navlist  li  {
    	margin-right: 10px;
        float: left;   
        color: #999;
    }
    .modalbox .navlist  li a {
    	color: #999;
    	display: block;
    	float: left;
    	line-height: 50px;
    }
    .modalbox .navlist  li a span {
    	position: relative;
    	top: 6px;
    	display: inline-block;
    	width: 30px;
    	height: 30px;
        background: url(/static/image/icons-sprite-25x25.png) no-repeat -340px -90px;
    }
	.modalbox .bigBox .contentBox {
		width: 996px;
		height: 629px;
		background: #FFF;
	    box-sizing: border-box;
	}
	.modalbox .bigBox .contentBox ul li {
		list-style: none;
	}
	.modalbox .bigBox .contentBox>div{
		float: left;
	}
	.modalbox .bigBox .contentBox .left {
		width: 270px;
	}
	.modalbox .bigBox .contentBox .center {
		width: 336px;
		margin: 0 34px;
	}
	.modalbox .bigBox .contentBox .right {
		width: 242px;
	}
   .modalbox .bigBox .contentBox .left h3 {
   	   letter-spacing:.1em;
   	   line-height: 40px;
   	   font-weight:normal;
   }
   .modalbox .bigBox .contentBox .left .sprite ul {
		width: 80px;
	}
	.modalbox .bigBox .contentBox .left .sprite ul li {
		border: 0;
		width: 15px;
		height: 15px;
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px;
		float: left;
	}
	.modalbox .bigBox .contentBox .left .sprite ul li:last-child {
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -350px -150px;
	}	
	.modalbox .bigBox .contentBox .left dt {
		font-weight: bold;
		line-height: 30px;
		padding: 10px 0;
	}
	.modalbox .bigBox .contentBox .left dd {
		line-height: 20px;
	}
	.modalbox .bigBox .contentBox .center {
		position: relative;
	}
	.modalbox .bigBox .contentBox .center .promotion {
      	width: 46px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		position: absolute;
		top: 10px;
		left: 10px;
		background-color: #000;
		color: #FFF;
	}
	.modalbox .bigBox .contentBox .center .big-picture {
		width: 100%;
		height: 400px;
		display: flex;
		vertical-align: middle;
		box-sizing: border-box;
		padding: 30px;
		/*display: none;*/
	}
	.modalbox .bigBox .contentBox .center .big-picture img {
		width: 100%;
	}
	.modalbox .bigBox .contentBox .center a {
		display: flex;
		width: 60px;
		height: 60px;
		float: left;
		border: 1px solid #ccc;
		vertical-align: middle;
		margin: 20px 8px; 
	}
	.modalbox .bigBox .contentBox .center a:nth-of-type(3)~a{
		display: none;
	}
	.modalbox .bigBox .contentBox .center a img {
		width: 100%;
	}
	.modalbox .bigBox .contentBox .right .capacity {
		border-bottom: 0.3px solid #ccc;
		line-height: 30px;
	}
	.modalbox .bigBox .contentBox .right .select-capacity {
		margin: 10px 0; 
	}
	.modalbox .bigBox .contentBox .right .number {
		border-top: 3px solid #000;
		line-height: 50px;
	}
	.modalbox .bigBox .contentBox .right .number .price {
		float: right;
	}
	.modalbox .bigBox .contentBox .right .purchase{
		display: block;
		width: 100%;
		height: 28px;
		background-color: #000;
		color: #fff;
		position: relative;
		border: 1px solid #000;	
		text-align: center;
		line-height: 28px;	
		margin: 20px 0;
		cursor: pointer;
	}	
	.modalbox .bigBox .contentBox .right .purchase:hover{
		background-color: #FFF;
		border: 1px solid #666;
		color: #000;
	}	
	.modalbox .bigBox .contentBox .right .purchase span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -128px;
		display: block;
		width: 20px;
		height: 20px;
		position: absolute;
		left: 75px;
		top: 4px;
	}
	.modalbox .bigBox .contentBox .right .purchase:hover span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -19px -128px;
	}
	.modalbox .bigBox .contentBox .right .share {
		line-height: 20px;
		margin-right: 15px;
		float: left;
	}
	.modalbox .bigBox .contentBox .right .share .wishlist span {
		display: inline-block;
		width: 20px;
		height: 20px;
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -120px -70px;
	}
	.modalbox .bigBox .contentBox .right .jiathis_style {
	   	margin: 20px 0;
	}
</style>


