<template>
	<div class="lunbo">
		<transition-group tag="ul" class="clearfix slide" name="image">
			<li v-for="(image,index) in img" :key="index" v-show="index===mark">
				<a href=""><img :src="image"></a>

			</li>
		</transition-group>
		<div class="bullet">
			<span v-for="(item,index) in img.length" :class="{'active':index === mark}" @click="change(index)" :key="index"></span>
		</div>
	</div>
</template>

<script>
export default({
	name:'lunbo',
	data(){
		return{
			mark:0,
			img:[
				'./../static/images/lunbo1.jpg',
				'./../static/images/lunbo2.jpg',
				'./../static/images/lunbo3.jpg',
				'./../static/images/lunbo4.jpg',
			]

		}
	},
	methods:{
		change(i){
			this.mark=i;
		},
		autoplay(){
			this.mark++;
			if(this.mark==4){
				this.mark=0;
				return;
			}
		},
		play(){
			setInterval(this.autoplay,6000)
		}

	},
	created(){
		this.play();
	}
})
</script>

<style scoped>
.lunbo{
	width: 100%;
    height: 400px;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.lunbo .slide{
	width: 100%;
    height: 400px;
}	

.lunbo .slide img{
	width: 100%;
	height: 400px;
	min-width: 1200px;
}

.lunbo li{
	position: absolute;
}

.bullet{
	width: 100%;
    position: absolute;
    /*bottom: 10px;*/
    margin: 0 auto;
    text-align: center;
   	z-index: 10;
   	top: 350px;
}
span{
    width: 20px;
    height: 5px;
    border: 1px solid;
    background: grey;
    display: inline-block;
    margin-right: 10px;
}
        
.active {
    background: red;
}
.image-enter-active{
	transform: translateX(0);
	transition: all 1s ease;
}	

.image-leave-active{
	transform: translateX(-100%);
	transition: all 1s ease;
}

.image-enter{
	transform: translateX(100%);
}

.image-leave{
	transform: translateX(0);
}
</style>


