<template>
<div class="registers">
	<NavHeader></NavHeader>
	<div class="register">
		<div class="center">
<!-- 		
			<span>
				<a href="">首页</a> >
				<a href="">我的账户</a>
			</span> -->
			<div class="center-left">
				<h1>新用户注册</h1>
				<span class="gender">
					性别：
						<input type="radio" name="gender" > 女士
						&emsp;
						<input type="radio" name="gender"> 男士
						&ensp;&ensp;

					(* 为必填)
				</span>
				<p class="mandatory">
					* 您的邮箱/手机: <br>
					<input type="text" v-model="telmail" @blur="checktelmail">
					<span v-if="err_message1">格式不正确</span>
					<span v-if="telmailexist">已被注册</span>
					<span v-if="telmailOk">可以注册</span>
				</p>
				<p class="mandatory">
					* 密码:<br>
					<input type="password"  v-model="password" @blur="checkPwd">
					<span v-if="err_message2">密码为6-10位</span>
					<span v-if="pwdOk">格式正确</span>
				</p>
				<p class="mandatory">
					* 确认密码: <br>
					<input type="password" v-model="repassword" @blur="checkPwd2">
					<span v-if="repwdOk">两次密码相同</span>
					<span v-if="err_message3">两次输出密码不一致</span>
				</p>
				<p class="mandatory">
					姓名:<br>
					<input type="text"  v-model="userName" @blur="checkname">
				</p>
				<p class="mandatory">
					请输入验证码:<br>
					<input type="text"><br>
					<span>
						<img src="./../images/yanzhenma.png" alt="">
						看不清,再<a href="">换一张</a>
					</span>
				</p>
				<p>
					<input type="checkbox"> 
					<strong>我愿意订阅圣罗兰电子杂志</strong><br>
					<span>我已阅读和了解圣罗兰官网的<a href="">使用条款</a>和<a href="">隐私声明</a>，并且同意接受使用条款。</span>
				</p>
				<button @click="register">同意协议并注册</button>
			</div>
			<div class="center-center">
				<p>我是已经注册用户,前往<a href="">登录</a></p>
				<span>使用合作网站登录账号</span>
				<div class="login">
					<a href="">
						<span><img src="./../images/login-icon-weibo.png" alt=""></span>
						新浪微博
					</a>
					<a href="">
						<span><img src="./../images/login-icon-qq.png" alt=""></span>
						QQ登录
					</a>
					<a href="">
						<span><img src="./../images/login-icon-alipay.png" alt=""></span>
						支付宝
					</a>
					<a href="">
						<span><img src="./../images/login-icon-wechat.png" alt=""></span>
						微信
					</a>
				</div>
			</div>
			<div class="center-right">
				<p>注册成为圣罗兰官网会员即可尊享</p>
				<div class="information">
					<span>获取最新商品信息</span>
					<p>已注册邮箱将第一时间<br>
					   获取特价促销、
					   新品发布、<br>礼盒
					   套装及更多会员专享资讯。</p>
				</div>
				<div class="information">
					<span>订单历史记录</span>
					<p>查看您的订单详情，<br>
					   及时了解物流配送状态。</p>
				</div>
				<div class="information">
					<span>生日礼遇</span>
					<p>完善个人生日信息，<br>
					   生日当月发现惊喜，<br>
					   届时请查看已注册邮箱。</p>
				</div>
				<div class="information">
					<span>快速购买通道</span>
					<p>我们会在您的账户中保存<br>历史账单和配送信息，使您<br>方便快捷地购物。</p>
					<p>(了解更多隐私政策)</p>
					<hr>
					<div class="fenge"></div>
				</div>
				<div class="service">
					<strong>客户服务</strong>
					<div>
						<span></span>
						400-820-6362
					</div>
					<p>（周一至周日 9:00-21:00）</p>
					<strong>联系我们</strong>
				</div>
			</div>
		</div>	
		<div class="fl"></div>
	</div>
	<Service></Service>
	<MyFooter></MyFooter>
</div>
</template>
<script>
	import axios from "axios"
	import '.././assets/css/normalize.css'
	import NavHeader from '@/components/navHeader'
	import Service from '@/components/service'
	import MyFooter from '@/components/myfooter'
	export default{
		name:"registers",
		data(){
			return{
				userId:"",
				userName:"",
				password:"",
				repassword:"",
				telmail:"",
				err_message1:false,
				err_message2:false,
				err_message3:false,
				telmailexist:false,
				telmailOk:false,
				pwdOk:false,
				repwdOk:false
			}
		},
		components:{
			NavHeader,
			Service,
			MyFooter
		},
		methods:{
			checktelmail(){
				var ereg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
				var treg = /^1[34578]\d{9}$/;
				var param ={
					telmail: this.telmail
				}
				if(ereg.test(this.telmail) || treg.test(this.telmail)){
					console.log(1);
      				//通过发送请求
        			axios.get('/users/checktelmail',{params:param}).then((response)=>{
	          			var res = response.data;
	          			//用户名不存在
          				if(res.status == '0'){
          					console.log(res.status);
            				this.telmailOk = true;
            				this.telmailexist =false;
            				this.err_message1 =false;
            				console.log(res.msg);
          				}else{
           				//用户名不存在的情形
            				if(res.status == '2'){
            					console.log(3);
            					this.telmailexist = true;
            					this.telmailOk =false;
            					this.err_message1 =false;
            				}
          				}
        			})
      				//不通过
      			}else{
      				console.log(2);
         			this.err_message1 = true;
         			this.telmailOk =false;
         			this.telmailexist =false;
      			}
			},
			checkPwd(){
				var reg = /^.{6,10}$/;
				if(reg.test(this.password)){
					this.pwdOk =true;
       				this.err_message2= false;
      			}
      			//如果没有通过
      			else{
         			this.err_message2 = true;
         			this.pwdOk =false;
     			}
			},
			checkPwd2(){
       			if(this.password != this.repassword){
        			this.err_message3 = true;
         			this.repwdOk =false;
      		 	}
       			else{
        			this.repwdOk =true;
       				this.err_message3= false;
       			}
			},
			checkname(){
				
			},
			register(){
				// console.log(this);
				//当发送Post请求时，将用户名/密码/cookie设置的长度传给后台
        		axios.post('/users/register',{telmail: this.telmail,password: this.password,userName: this.userName}).then((response)=>{
          			var res = response.data;
          			if(res.status == '0'){
            			//注册成功后 
           				 console.log(res.msg);
            			//跳转到主页
            			this.$router.push({path:'/',query:{'status':1,'telmail':this.telmail}})
          			}else{
           
         			}
        		})
			}
		}
	}
</script>
<style>
	.fl{
		clear:both;
	}
	.register{
		margin-top: 20px;
		font-size: 14px;
	}
	.center{
		width: 996px;
		margin: 0 auto;
	}
	.center .center-left{
		width: 350px;
		height: 700px;
		float: left;
	}
	.center .center-left .gender{
		font-size: 11px;
	} 
	.center .center-left .gender input{
		vertical-align: text-bottom;
		font-size: 10px;
	} 
	.center .center-left p{
		margin-top: 20px;
		line-height: 20px;
	}
	.center .center-left .mandatory input{
		width: 300px;
		height: 35px;
	}
	.center .center-left button{
		width: 200px;
		height: 30px;
		background-color:#000 ;
		margin-top: 30px;
		color:white;
	}
	.center .center-center{
		width: 350px;
		height: 700px;
		border-right:1px solid #ccc;
		float: left;
	}
	.center .center-center p{
		line-height: 100px;
		text-align: center;
		padding-top: 50px;
	}
	.center .center-center span{
		display: block;
		text-align: center;
	}
	.center .center-center .login{
		margin-top: 30px;
		margin-left: 30px;
	}
	.center .center-center .login a>span{
		display: inline-block;
		width: 20px;
		height: 20px;
		vertical-align: middle;
	}
	.center .center-right{
		width: 200px;
		height: 700px;
		float: right;
	}
	.center .center-right>p{
		font-size: 13px;
		line-height: 40px;
	}
	.center .center-right span{
		font-size: 13px;
		line-height: 40px;
		font-size: 17px;
	}
	.center .center-right .information{
		margin-top: 20px;
	}
	.center .center-right .information>p{
		font-size: 10px;
		line-height: 20px;
		color:#999999;
	}
	.center .center-right .fenge{
		width: 250px;
		height: 10px;
		margin: -6px auto;
		background: url('../../static/image/a7.png') no-repeat;
		background-position: -327px -51px;
	}
	.center .center-right hr{
		margin-top: 10px;
		border:1px solid #000;
	}

	.center .service strong{
		display: inline-block;
		padding-top: 30px;
	}
	.center .service>div{
		margin-top: 10px;
	}
	.center .service>div span{
		display: inline-block;
		width: 20px;
		height: 20px;
		vertical-align: middle;
		background: url('../../static/image/a7.png') no-repeat;
		background-position: -476px 2px;
		
	}
	.center .service p{
		font-size: 10px;
		padding-top: 10px;
		color:#999999;
	}
</style>


