<template>
	<div class="shoppingCart">
		<div class="header">
			<div class="container">
				<router-link class="logo" to="/">
					<img src="/static/images/logo.png" alt="">
				</router-link>
				<div class="contact-us">
					<p>24小时免费咨询电话：400-820-6362</p>
					<span>或联系我们的<a href="##">在线客服</a></span>
				</div>
			</div>
		</div>
		<div class="main">
			<div class="container">
				<ul class="order-status">
					<li class="active">1 我的购物袋</li>
					<li>2 填写核对订单</li>
					<li>3 成功提交订单</li>
				</ul>		
				<h1>我的购物袋</h1>		
				<div class="goods">
					<p v-if="showNull">您的购物袋是空的。立即购买您最喜爱的产品</p>
					<!-- <div class="settlement">
						<a href="##">< 继续购物</a>
						<a href="##">去结算</a>
					</div> -->
					<div v-if="showGoods">
						<table class="goods-list" border="0" cellspacing='0'>
							<tr class="goodslist-information">
								<th><input type="checkbox"></th>
								<th>全选</th>
								<th style="width: 280px">商品</th>
								<th>单价</th>
								<th>数量</th>
								<th>价格</th>
							</tr>
							<tr class="a-goods" v-for="items in cartLists">
								<td><input type="checkbox" v-bind:class="{'check':items.checked=='1'}" @click="editCart('checked',items)">
								</td>
								<td class="goods-picture"><img :src="items.goodsImg" alt=""></td>
								<td class="goods-information">
									<p class="goods-name">{{items.goodsChName}}</p>
									<p class="goods-color">{{items.goodsColor}}</p>
									<div>
										<router-link to="/">修改</router-link>
										<a href="javascript:;" @click="delCart(items.goodsId)">删除</a>｜
										<a href="##" class="wish-list"><span></span>心愿单</a>
									</div>
								</td>
								<td class="price">{{items.goodsPrice|currency('￥')}}</td>
								<td>
									<a class="input-sub" @click="editCart('minu',items)">-</a>
                        			<span class="select-ipt">{{items.goodsBuyNum}}</span>
                        			<a class="input-add" @click="editCart('add',items)">+</a>
								</td>
								<td class="price">{{(items.goodsPrice*items.goodsBuyNum)|currency('￥')}}</td>
							</tr>
						</table>
						<div class="activity"> 
							<p>七夕刻字服务已结束，更多活动信息请关注官网</p>
							<h3>【官网专属礼遇】</h3>
							<ul>
								<li>·&emsp; 官网任购享随行装，首次购物可获享专属会员入会三件礼</li>
								<li>·&emsp; 购物满850元，享「妍活青春」护肤体验装两件礼</li>
								<li>·&emsp; 购物满1000元，加享纯白面霜旅行装</li>
							</ul>
						</div>
						<div class="promotion">
							<p>
								促销代码 <input type="text" placeholder='请输入促销代码'>
								<button>提交</button>
							</p>
						</div>
						<table class="total" border="0" cellspacing='0' >
							<tr class="goods-total">
								<td>
									<p>商品总价</p>
									<p>运费</p>								
								</td>
								<td>
									<p>{{totalPrice|currency('￥')}}</p>
									<p>￥10</p>								
								</td>							
							</tr>
							<tr class="total">
								<td><p>总价</p></td>
								<td><p>{{totalPrice+10|currency('￥')}}</p></td>
							</tr>
						</table>
						<div class="settlement">
							<router-link to="/lists">继续购物</router-link>	
							<router-link to="/order">去结算</router-link>
						</div>
					</div>                    
				</div>
				<div class="service">
					<div class="pay">
						<ul>
							<li><span></span>安全支付保障</li>
							<li>安全支付系统采用SSL加密。</li>
							<li>
								<img src="/static/image/payment.jpg" alt="">
							</li>
						</ul>
					</div>
					<div class="distribution">
						<ul>
							<li><h3>全国配送</h3></li>
							<li>快递可至全国各地</li>
							<li>快递2至6日正常送达全国</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		<div class="footer">
            <div class="container">
	           <ul class="service">
	            	<li>
	            		<h4><span></span>需要帮助？</h4>
	            		<p>我们在线客服人员将帮助您在线选购商品</p>
	            		<a href="##">开始在线对话</a>
	            	</li>
	            	<li>
	            		<h4><span></span>联系我们</h4>
	            		<p>官方订购热线：<br>
							400-820-6362 <br>
						  （周一至周日 9:00-21:00）
						</p>
	            		<a href="##">联系我们</a>
	            	</li>
	            	<li>
	            		<h4><span></span>您的特殊礼遇</h4>
	            		<p>. &emsp;首购好礼 <br>
	            			首次购物可获赠免费产品小样
	            		</p>
	            	</li>
	            	<li>
	            		<h4><span></span>安全支付保障</h4>
	            		<p>安全支付系统采用SSL加密。</p>
	            		<img src="/static/image/payment.jpg" alt="">
	            	</li>   
	            	<li>
	            		<h4><span></span>全国配送</h4>
	            		<p>快递可至全国各地。</p>
	            		<p>快递2至6日正常送达全国</p>
	            	</li>             	         	            	            	
	            </ul>  
	            <div class="subscribe">
	            	<p>
	            		订阅圣罗兰电子杂志 <input type="text" placeholder='请输入您的邮箱'>
							<button>提交</button>
	            	</p>
	            </div>          	
            </div>
		</div>
		<myFooter></myFooter>	
	</div>
</template>
<script>
	import axios from 'axios'
	import myFooter from '@/components/myfooter'
	import {currency} from './../util/currency'
	export default {
		name:"shoppingCart",
		data(){
			return{
				cartLists:[],
				showNull:true,
				showGoods:true
			}
		},
		components:{
			myFooter
		},
		mounted(){
			this.init();
		},
		computed:{
			// checkAllFlag(){
			// 	return this.checkedCount ==this.cartLists.length;
			// },
			// checkedCount(){
			// 	var i=0;
			// 	this.cartLists.forEach((items)=>{
			// 		if(items.checked =="1"){
			// 			i++
			// 		}
			// 	})
			// 	return i;
			// }
			totalPrice(){
				var money =0;
				this.cartLists.forEach((items)=>{
					if(items.checked=='1'){
						money+=parseInt(items.goodsPrice)*parseInt(items.goodsBuyNum);
					}
				})
				return money;
			}
		},
		methods:{
			init(){
				axios.get('/users/cartLists').then((response)=>{
					let res =response.data;
					this.cartLists =res.result;
				})
			},
			delCart(goodsId){
				axios.post('users/delCart',{
					goodsId:goodsId
				}).then((response)=>{
					let res=response.data;
					if(res.status=='0'){
						this.init();
						this.$store.commit("updateCartCount",-goodsBuyNum);
					}
				})
			},
			editCart(flag,items){
                if(flag=='add'){
                  items.goodsBuyNum++;
                }else if(flag=='minu'){
                  if(items.goodsBuyNum<=1){
                    return;
                  }
                  items.goodsBuyNum--;
                }else{
                  items.checked = items.checked=="1"?'0':'1';
                }

                axios.post("/users/cartEdit",{
                  goodsId:items.goodsId,
                  goodsBuyNum:items.goodsBuyNum,
                  checked:items.checked
                }).then((response)=>{
                    let res = response.data;
                    if(res.status=="0"){
                      this.$store.commit("updateCartCount",flag=="add"?1:-1);
                    }
                })
            }
           	// toggleCheckAll(){
           	// 	this.checkAllFlag = !this.checkAllFlag;
           	// 	this.cartLists.forEach((items)=>{
           	// 		items.checked =this.checkAllFlag;
           	// 	})
           	// }
		}
	}
</script>
<style scoped>
 	@import url('../assets/css/normalize.css');
    ul li {
    	list-style: none;
    }
	.shoppingCart .container {
		width: 996px;
		margin: 0 auto;
	}
	.shoppingCart .header {
		width: 100%;
		height: 85px;
		background-color: #070707;
	}
	.shoppingCart .header .logo {
		float: left;
		margin-top: 22px;
	}
	.shoppingCart .header .contact-us {
		float: right;
		color: #C0C6C6;
	}
	.shoppingCart .header .contact-us p {
		margin-top: 15px;
		line-height: 30px;
	}
	.shoppingCart .header .contact-us span {
		line-height: 20px;
		float: right;
	}
	.shoppingCart .header .contact-us a {
		color: #FFF;
	}
	.shoppingCart .main {
		width: 100%;
		overflow: hidden;
	}
	.shoppingCart .main .order-status {
		width: 100%;
		overflow: hidden;
	}
	.shoppingCart .main .order-status li {
		width: 300px;
		height: 60px;
		line-height: 60px;
		font-size: 16px;
		float: left;
		letter-spacing:2px;
		color: #B1AAAF;
		margin-right: 30px;
		border-bottom: 3px solid #B1AAAF;
	}
	.shoppingCart .main .order-status li.active{
		color: #000;
		font-weight: bold;
		border-bottom: 3px solid #000;
	}
    .shoppingCart .main .goods{
    	float: left;
    	width: 672px;
    	overflow: hidden;
    }
     .shoppingCart .main .goods a {
     	line-height: 30px;
     }
     .shoppingCart .main .settlement {
     	padding: 10px 0;
     }
