     	display: block;
     	background-color: #D29F56;
     	color: #FFF;
     	width: 98px;
     	height: 28px;
     	line-height: 30px;
     	text-align: center;
     	border: 1px solid #D29F56;
     	float: right;
     }
     .shoppingCart .main .settlement a:last-child:hover{
     	background-color: #FFF;
     	color: #D29F56;
     }
     .shoppingCart .main .goods .goods-list tr:first-child {
     	height: 60px;
     	background-color: #000;
     	color: #FFF;
     }
