     	text-align: center;
     }
     .shoppingCart .main .goods .goods-list tr th {
     	width: 80px;
     }
     .shoppingCart .main .goods .goods-list tr td {
		border-top: 1px solid #ccc;
     }
     .shoppingCart .main .goods .goods-list tr+tr {
     	height: 118px;
     }
     .shoppingCart .main .goods .goods-list tr .goods-picture img {
