     }
     .shoppingCart .main .goods .goods-list tr .goods-information p {
     	line-height: 20px;
     }
    .shoppingCart .main .goods .goods-list tr .goods-information .goods-color {
    	color:#888;
    }
    .shoppingCart .main .goods .goods-list tr .goods-information a {
    	color: #666;
    }
	.shoppingCart .main .goods .goods-list tr .goods-information .wish-list span {
		display: inline-block;
		width: 20px;
		height: 20px;
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -120px -66px;
	}     
	.shoppingCart .main .goods .goods-list tr .price {
		font-weight: bold;
	}
	.shoppingCart .main .goods .activity p{
		color: #FF3737;
		font-weight: bold;
		line-height: 24px;
	}
	.shoppingCart .main .goods .activity h3 {
		line-height: 50px;
	}
	.shoppingCart .main .goods .activity ul li {
		color: #666;
	}
	.shoppingCart .main .goods .promotion {
		margin: 20px 0 ;
	}
	.shoppingCart .main .goods .promotion p {
		line-height: 50px;
		border-top: 1px solid #ccc;
		color: #8A8A8A;
	}
	.shoppingCart .main .goods .promotion input{
