	}
	.shoppingCart .main .goods .promotion button{
		height: 30px;
		width: 92px;
		cursor: pointer;
		background-color: #000000;
		color: #FFF;
		line-height: 28px;
		text-align: center;
		margin-left: 5px
	}
	.shoppingCart .main .goods .total tr{
       height: 75px;
	}
	.shoppingCart .main .goods .total tr p {
		line-height: 30px;
	}
