		text-align: right;
	}
	.shoppingCart .main .goods .total tr td:last-child {
		width: 85px;
		text-align: center;
	}
	.shoppingCart .main .service {
		float: right;
	}
   .shoppingCart .main .service ul li {
		line-height: 25px;
		padding:10px 0;
	}
	.shoppingCart .main .service ul li+li {
		color: #ACACAC;
	}
	.shoppingCart .main .service .pay,
	.shoppingCart .main .service .distribution{
		width: 160px;
		height: 175px;
		border: 5px solid #E4E4E4;
		padding: 30px;
