		background: url(/static/image/icons-sprite-25x25.png) no-repeat -110px -100px;
		margin-right: 5px;
		position: relative;
		top: 5px;
	}
	.shoppingCart .main .service .pay ul li:first-child {
		font-weight: bold;
		font-size: 16px;
	}
	.shoppingCart .footer .service {
		padding: 50px 0 20px;
		border-top:1px solid #E4E4E4;
		margin: 30px 0;
		width: 1010px;
		overflow: hidden;
	}
    .shoppingCart .footer .service li {
    	float: left;
