    }
    .shoppingCart .footer .service li p {
    	margin: 20px 0;
    	color: #999;
    }
    .shoppingCart .footer .service li a {
    	color: #666;
    }
    .shoppingCart .footer .service li h4 span{
    	display: inline-block;
    	width: 25px;
    	height: 25px;
    	margin-right: 5px;
    	position: relative;
        top: 5px;
    }
    .shoppingCart .footer .service li:nth-child(1) h4 span{
