<template>
	<div class="detail-center">
		<div class="type-page">
			<hr>
			<span></span>
			<div>
				<p>{{title}}</p>
			    <div v-for='item in items'>
				  <img :src="item" alt="">
			    </div>
			    <span></span>
			</div>
			<div class="dare-to-share">
				<div class="yslbeauty yslbeauty-left">
					<h2>Dare to Share</h2>
					<h4>#yslbeauty</h4>
				</div>
				<div class="yslbeauty yslbeauty-center">
					<img src="/static/images/down.jpg" alt="">
				</div>
				<div class="yslbeauty yslbeauty-right">
					<p class="first">SOYEZ LA</p>
					<h5>PREMIERE A</h5>
					<h5>AFFICHER</h5>
					<p>VOTRE</p>
					<p>STYLE</p>
				</div>
			</div>

		</div>
	</div>
</template>
<script>
// 曝光组件
export default {
  name: 'detailPages',
  data () {
    return {
      title:"超模绝密",
      items:['/static/images/pc_video.jpg']
    }
  }
}
</script>
<style scoped>
	.type-page{
		width: 996px;
		margin: 0 auto;
	}
	.type-page hr{
		margin-top: 20px;
		border:1px solid #ccc;
	}
	.type-page>span{.type-page .comment-right
		display:block;
		width: 140px;
		height: 10px;
		margin:0 auto;
		background-image: url('/static/images/a7.png');
		background-position: 141px -51px;
		margin-top: -5px;
	}
	.type-page img{
		margin-top: 20px;
	}
	.type-page>div>p{
		display: block;
		width: 120px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		margin: 10px auto;
		font-size: 18px;
	}
	.type-page>div>span{
		display:block;
		width: 140px;
		height: 10px;
		margin:0 auto;
		margin-top: -7px;
		position: relative;
		z-index: 5;
		background-image: url('/static/images/a7.png');
		background-position: 141px -51px;
	}
	.detail-center .type-page .dare-to-share{
		margin-top: 30px;
		overflow: hidden;
	}
	.detail-center .type-page .dare-to-share .yslbeauty{
		width: 332px;
		height: 300px;
		float: left;
	}
	.type-page .dare-to-share .yslbeauty-left h2{
		text-align: center;
		padding-top: 90px;
		font-size: 31px;
	}
	.type-page .dare-to-share .yslbeauty-left h4{
		text-align: center;
		padding-top: 30px;
		font-size: 24px;
	}
	.type-page .dare-to-share .yslbeauty-center img{
		width: 91%;
		margin: 0;
		padding: 0;
	}
	.type-page .dare-to-share .yslbeauty-right{
		background-color: #000;
		color: white;
		text-align: center;
		font-size: 24px;
		line-height: 30px;
	}
	.type-page .dare-to-share .yslbeauty-right .first{
		margin-top: 70px;
	}
</style>


