<template>
<div class="searchPage">
	<NavHeader></NavHeader>
	<div class="goods-list">
		<ul>
			<li class="goods" v-for="(item,index) in items">					   
				<a href="##" class="goods-picture">
					<p class="promotion" v-text="item.goodsKind" v-show="item.goodsKind"></p>
					<p>
						<img :src="item.goodsImg" alt="">
					</p>
					<h3 class="chinese-title" v-text="item.goodsEnName"></h3>
					<div class="bgbox">
						<a href="##" @click="modalbox(item)">
							<span>快速浏览</span>
						</a>
					</div>
				</a>					
				<h3 class="english-title" v-text="item.goodsChName"></h3>
				<p class="price" v-text="item.goodsPrice"></p>
				<button class="purchase" @click="modalbox(item)"><span></span>立即购买</button>
				<router-link class="routerink" :to="{path:'details',query:{goodsId:item.goodsId}}">
					<button class="details" ><span></span>查看详情</button>
				</router-link>
				<div class="buttonBgbox"></div>				
			</li>											
		</ul>
	</div>
	<modalbox :goodsOne="goodsOne"></modalbox>
	<Service></Service>
	<myFooter></myFooter>
</div>
</template>

<script>
	import NavHeader from "@/components/navHeader"
	import modalbox from "@/components/modalbox"
	import Service from '@/components/service'
	import myFooter from '@/components/myfooter'
	import axios from 'axios'
	export default{
		name:'searchPage',
		data(){
			return{
				items:[],
				goodsOne:{}
			}
		},
		components:{
			NavHeader,
			modalbox,
			Service,
			myFooter
		},
		methods:{
			modalbox(item){
  			$('.modalbox-bg,.modalbox').show(); 	
  			this.goodsOne=item;	
  			}
		},
		created(){
			var title =this.$route.query.title;
			console.log(title);
			// axios.get({
			// 	url:'/searchPa',
			// 	params:{title:title}
			// }).then((data)=>{
			// 	this.items =data.data.data;
			// })
			axios.get('/goods/searchPa',{
				params:{
					title:title
				}
			}).then((response)=>{
				let res =response.data;
				this.items =res.data;
			})
		}
	}
</script>

<style scoped> 
    ul li {
    	list-style: none;
    }
	.searchPage .goods-list ul li {
		float: left;
		height: 550px;
    }
    .searchPage .goods-list ul li:first-child{
    	margin: 5px ;
    }
	.searchPage .goods-list ul .goods{
		box-sizing: border-box;
		width: 239px;
		border: 1px solid #ccc;
		position: relative;
		margin: 5px;
	}
	.searchPage .goods-list ul .goods .promotion {
		width: 46px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		position: absolute;
		top: 10px;
		left: 10px;
		background-color: #000;
		color: #FFF;
	}
	.searchPage .goods-list ul .goods a {
		display: block;
	}
	.searchPage .goods-list ul .goods .goods-picture {
		padding-top: 40px;
	}
	.recommend .goods-list ul .goods .goods-picture {
		position: relative;
		z-index: 5;
	}
	.searchPage .goods-list ul .goods a p  {
		width: 190px;
		height: 280px;
		margin: 0 auto;
	}
	.searchPage .goods-list ul .goods a p img {
		width: 100%;
		height: 100%;
	}
	.searchPage .goods-list ul .goods h3 {
		text-align: center;
		margin-bottom: 10px;
		font-size: 14px;
		color: #333;
		letter-spacing: .25px;
		line-height: 18px;
	}
	/*评价*/
	.searchPage .goods-list ul .goods .sprite {
		height: 15px;
		margin: 5px 0;
	}
	.searchPage .goods-list ul .goods .sprite ul {
		width: 80px;
		margin: 20px auto;
	}
	.searchPage .goods-list ul .goods .sprite ul li {
		border: 0;
		width: 15px;
		height: 15px;
		margin: 0;
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px;
		float: left;

	}
	.searchPage .goods-list ul .goods .sprite span {
		display: none;
	}
	.searchPage .goods-list ul .goods .price {
		text-align: center;
		margin: 10px 0;
		line-height: 40px;
	}
	.searchPage .goods-list ul .goods button {
		display: block;
		margin: 5px auto;
		width: 168px;
		height: 28px;
		background-color: #000;
		color: #fff;
		position: relative;
		cursor: pointer;
		border: 1px solid #000;
	}
	.searchPage .goods-list ul .goods .details {
		background-color: #E6E5E5;
		color: #666;
		border: 1px solid #E6E5E5;
	}
	.searchPage .goods-list ul .goods button span {
		display: block;
		width: 20px;
		height: 20px;
		position: absolute;
		left: 35px;
		top: 4px;
	}
	.searchPage .goods-list ul .goods .purchase span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -128px;
	}
	.searchPage .goods-list ul .goods .details span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -322px;
	}
	.searchPage .goods-list ul .goods .purchase:hover,
	.searchPage .goods-list ul .goods .details:hover{
		background-color: #FFF;
		border: 1px solid #666;
		color: #000;
	}
	.searchPage .goods-list ul .goods .purchase:hover span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -19px -128px;
	}
	.searchPage .goods-list ul .goods .bgbox {
		width: 100%;
		height: 340px;
		position: absolute;
        top: 0;
        left: 0;
		background-color: rgba(255,255,255,.6);
		display: none;
	}
	.searchPage .goods-list ul .goods .bgbox a {
		display: block;
		width: 100%;
		height: 338px;
	}
	.searchPage .goods-list ul .goods .bgbox a span {
		width: 100px;
		line-height: 20px;
		color: #000;
		text-align: center;
		position: absolute;
		top:129px ;
		left: 50%;
		margin-left: -50px;
		border-top: 1px solid #666;
        border-bottom: 1px solid #666;
	}
	.searchPage .goods-list ul .goods .buttonBgbox {
		width: 100%;
		height: 210px;
		position: absolute;
		left: 0;
		top: 338px;
		background-color: rgba(255,255,255,.6);
		z-index: 6;
		display: none;
	}	    
		/*特效*/
    .searchPage .goods-list ul .goods .goods-picture:hover .bgbox{
    	display: block;
    }
    .searchPage .goods-list ul .goods .goods-picture:hover~.buttonBgbox {
    	display: block;
    }
    .goods-list{
    	overflow: hidden;
    }		
</style>


