import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'
import homepageGoodslists from '@/views/homepageGoodslists'
import GoodsList from '@/views/GoodsList'
import register from '@/views/register'
import shoppingCart from '@/views/shoppingCart'
import details from '@/views/details'
import orders from '@/views/order'
import sucOrder from '@/views/sucOrder'
import searchPage from '@/views/searchPage'
import userName from '@/views/userName'
Vue.use(Router)

export default new Router({
	 mode:'history',
  	routes: [
    {
      //首页
      path: '/',
      name: 'homepageGoodslists',
      component: homepageGoodslists
    },
    {
      //列表页
      path: '/lists',
      name:'GoodsList',
      component: GoodsList
    },
    {
      //注册页
      path: '/register',
      name:'register',
      component: register
    },
    {
      //购物车
      path:'/shoppingCart',
      name:'shoppingCart',
      component: shoppingCart
    },
    {
      //详情页
    	path: '/details',
      name:'details',
    	component: details
    },
    {
      //订单页面
      path: '/order',
      component: orders
    },
    {
      //成功提交订单页面
      path:'/sucOrder',
      component: sucOrder
    },
    {
      //搜索页面
      path:'/searchPage',
      component: searchPage
    },
    {
      //个人中心
      path:'/personalCenter',
      component: userName
    }
  ]
})



