				<ul>
					<li>总评价</li>
					<li>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
					</li>
					<li>请填写评论</li>
					<li>添加您的评论</li>
				</ul>
				<hr>
			</div>
			<!-- 评论内容 -->
			<div class="comment-content">
				<div class="head-portrait">
					<img src="/static/images/touxiang.png" alt="">
					150****3986
				</div>
				<div class="comment-center">
					<p>{{title}}</p>
					<div class="xingji">
						<p>线上官网购买</p>
						<p>2017-03-24 11:35:47</p>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
					</div>
				</div>
				<div class="pinglun"> 
					<p>有18位用户认为该评论有用</p>
					<h4>该评论：</h4>
					<button>有用</button>
				</div>
			</div>
			<div class="hengxian"></div>
		</div>
		
	</div>
</template>
<script>
	export default{
		name:'comment',
		data (){
			return{
				title:'对于这颜值，简直是香水中的战斗机！！多变形的玻璃瓶，质感很高。黑色蝴蝶结也非常的漂亮精致。 '			
			}
		}
	}
</script>
<style scoped>
	.comment{
		overflow: hidden;
	}
	.type-page{
		width: 996px;
		margin: 0 auto;
	}
	.type-page hr{
		margin-top: 50px;
		border:1px solid #ccc;
	}
	.type-page>span{
		display:block;
		width: 140px;
		height: 10px;
		margin:0 auto;
		background-image: url('/static/images/a7.png');
		background-position: 141px -51px;
		margin-top: -5px;
	}
	.type-page .comment-left{
		width: 200px;
		height: 150px;
		float: left;
	}
	.type-page .comment-left img{
		width: 100px;
		margin-left: 50px;
	}
	.type-page .comment-right{
		width: 796px;
		height: 150px;
		/*border:1px solid red;*/
		float: left;
	}
	.type-page .comment-right h1{
		text-align: center;
		padding-top: 30px;
	}
	.type-page .comment-right ul li {
		width: 150px;
		height: 30px;
		float: left;
		margin-left: 40px;
		margin-top: 20px;
		text-align: center;
		line-height: 30px;
		list-style: none;
		font-size: 14px;
		overflow: hidden;
	}
	.type-page .comment-right ul{
		width: 780px;
		height: 50px;
	}
	.type-page .comment-right ul li span{
		display: block;
		width: 15px;
		height: 15px;
		float: left;
		margin-top: 7px;
		background: url('/static/images/a7.png') no-repeat -350px -150px;
	}
	.type-page .comment-right ul li:last-child{
		background-color: #000;
		color:white;
	}
	.type-page .comment-right ul li:last-child:hover{
		color:#000;
		border:1px solid #ccc;
		background-color: white;
		cursor:pointer;
	}
	.type-page .comment-right hr{
		border:1px solid #000;
		margin-top: 20px;
	}
	.type-page .comment-content{
		width: 996px;
		/*border:1px solid red;*/
		float: right;

	}
	.type-page>hr{
		border:1px solid #ccc;
		margin-top: 80px;
	}
	.type-page .hengxian{
		width: 996px;
		height: 1px;
		border-bottom:3px solid #000;
		overflow: hidden;
	}
	/*评论内容*/
	.comment-content .head-portrait,.comment-center,.pinglun{
		float: left;
		margin-left: 20px;
		margin-top: 30px;
	}
	.comment-content .head-portrait{
		width: 160px;
		height: 160px;
		border:1px solid #ccc;
		text-align: center;
	}
	.comment-content .head-portrait img{
		margin:0 auto;
		display: block;
		width: 90%;
	}
	.comment-content .comment-center{
		width: 600px;
		height: 160px;
	}

	
	.comment-content .pinglun {
		width: 150px;
		height: 160px;
		font-size: 14px;
		line-height: 20px;
	}
	.comment-content .pinglun button{
		width: 150px;
		height: 30px;
		display: block;
		margin: 30px auto;
		background-color: #000;
		color:white;
	}
	.comment-content .pinglun button:hover{
		background-color: white;
		color: #000;
		border:1px solid #ccc;
	}
	.comment-content .comment-center p{
		line-height: 20px;
		font-size: 14px;
	}
	.comment-content .comment-center .xingji{
		margin-top: 40px;
	}
	.comment-content .comment-center .xingji span{
		display: block;
		width: 15px;
		height: 15px;
		float: left;
		background: url('/static/images/a7.png') no-repeat -325px -150px;
	}
</style>



