<template>
	<div class="userInformation">
		<div class="information">
			<div class="left">
		        <p>
		       		<span>欢迎&emsp;{{userOrder.userName}}</span>
		       		<!-- <a href="##">登出</a> -->
		       	</p>	
		       	<p>欢迎您注册成为圣罗兰官网会员，我们很高兴为您服务。如果您有任何问题，请与我们联系。</p>	
		       	<p>如果您的个人信息有变, 请在此 <a href="##">更新个人信息</a>，完善生日信息，生日当月发现惊喜。</p>	
		       	<p>
		       		<a href="##">开始购物</a>
		       		<a href="##">查看个人信息</a>
		       	</p>
			</div>
			<div class="right">
	          <div class="service">
	              <h3>客户服务</h3>
	              <p>
	                <span></span>400-820-6362
	              </p>
	              <p>（周一至周日 9:00-21:00）</p>
	              <p>联系我们</p>
	          </div>				
			</div>			
		</div>
		<div class="goods-list">
			<p class="line"></p>
			<p class="title">您可能感兴趣</p>
			<ul>
				<li v-for="item in goodsList">
					<a href="##">
						<img :src="item.goodsImg" alt="">
						<h4 v-text="item.goodsChName"></h4>
					</a>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
import axios from 'axios';
export default {
	data(){
		return {
			goodsList:[],
			userOrder:{}
		}
	},
	mounted(){
		//请求用户数据
		axios.get('/users/userList').then((response)=>{
          			var res = response.data;
          			if(res.status == '0'){
          				this.userOrder=res.result;
          			}			
		})
		//请求商品数据
		axios.get('/goods/lists')
		.then((data)=>{
			for(var i=0;i<5;i++){
				this.goodsList.push(data.data[i]);
			}
		},(err)=>{
			console.log(err)
		});		
	}
}	
</script>
<style scoped>
	ul li {
		list-style: none;
	}
	.userInformation .information {
		overflow: hidden;
		padding:50px  20px;
	}
	.userInformation .information .left {
        width: 700px;
        padding-right: 30px;
        float: left;
        border-right: 1px solid #ccc;
    }
    .userInformation .information .right{
      float: left;
      width: 200px;
      padding-left: 25px;
    }
    .userInformation .information .left p+p {
    	margin: 20px 0;
    	color: #888;
    	font-size: 14px;
    }
    .userInformation .information .left p span {
    	font-size: 30px;
    	margin-right: 30px;
    }
    .userInformation .information .right .service h3 {
    	margin-bottom: 30px;
    	font-size: 16px;
    }
    .userInformation .information .right .service p {
        line-height: 20px;
        font-size: 14px;
      }
    .userInformation .information .right .service p:nth-of-type(1),
    .userInformation .information .right .service p:nth-of-type(3){
        color: #000;
        font-weight: bold;
      }
    .userInformation .information .left p:last-child a{
       display: inline-block;
       width: 120px;
       height: 30px;
       line-height: 30px;
       background-color: #000;
       color: #FFF;
       border: 1px solid #000;
       text-align: center;
       margin: 30px 20px 30px 0;
    }
    .userInformation .information .left p:last-child a:hover{
      background-color: #FFF;
      color: #000;
    }
    .userInformation .information .right .service p span{
        display: inline-block;
        position: relative;
        top: 5px;
        width: 30px;
        height: 20px;
        background: url(/static/image/icons-sprite-25x25.png) no-repeat -475px 0;
     }
     .userInformation .goods-list {
     	border-top: 1px solid #ccc;
     	position: relative;
     }
     .userInformation .goods-list .line  {
	    position: absolute;
	    left: 50%;
	    top: 0;
	    transform: translate(-50%,-50%);
		width: 140px;
		height: 13px;
		background: url(/static/images/icons-sprite-25x25.png) no-repeat -375px -50px;  
	}
	.userInformation .goods-list .title {
		position: relative;
		left: -20px;
		margin: 30px 0;
		text-align: center;
		font-size: 16px;
	}
	.userInformation .goods-list ul {
		width: 970px;
        box-sizing: border-box;
        overflow: hidden;
	}
    .userInformation .goods-list ul li {
		margin: 10px 0;
		width: 150px;
        padding: 20px;
      	overflow: hidden;
		float: left;
    }
    .userInformation .goods-list ul li a {
    	display: block;
    }
    .userInformation .goods-list ul li a img {
    	width: 80%;
    }
    .userInformation .goods-list ul li a h4 {
    	padding-top: 20px;
    	font-size: 13px;
    	color: #888;
    	text-align: center;
    }
</style>


