<template>
  <header class="header">
    <div class="header-top">
      <div class="container">
        <div class="header-hide" v-show="isShows">
          <div class="login_page account_login_page login_popup clearfix">
            <h2 class="account_header_title">
              <span class="account_header">用户登录</span>
            </h2>
            <div class="block block_create">
              <h2 class="section_header">新用户</h2>
              <p>
                "加入YSL官网的过程很简单,"<br>
                "您只需点击“立即注册”, "<br>
                "即可进入注册过程, "<br>
                "尊享会员专属权益。"
              </p>
              <router-link to="/register" class="button"> 
                <span >立即注册</span>
              </router-link>
            </div>
            <div class="block block_login">
              <h2 class="section_title"></h2>
              <form action="" method="post" id="dwfrm_login">
                <div id="missmatch" class="error_message" v-show="errTip">对不起，登录失败。请检查你是否输入有误，请再试一遍。</div>
                <fieldset>
                  <div class="f_field f_field_textinput  formfield formfield_textinput formfield_username required">
                    <input type="text" class="textinput f_textinput f_emailormobile required" placeholder="邮箱或手机" v-model="telmail">
                    <span class="error_message f_error_message"></span>
                  </div>
                  <div class="f_field f_field_password  formfield formfield_password required">
                    <input type="password" class="textinput f_password required" placeholder="密码"  v-model="password" @keyup.enter="login">
                    <span class="error_message f_error_message"></span>
                  </div>
                  <div class="f_field f_field_checkbox  formfield formfield_checkbox formfield_remember_me f_checked">
                    <input type="checkbox" class="checkbox f_checkbox">
                    <label for="dwfrm_login_rememberme" class="f_label">
                      <span class="f_label_value">记住我</span>
                    </label>
                    <div class="formfield formfield_link">
                      <a href="javascript:;" class="password_reset">
                        忘记密码
                      </a>
                    </div>
                    <div class="formfield formfield_button">
                      <button type="submit" class="button submit_button mobile_login" value="登录" @click.prevent="login()">
                        <span>登录</span>
                      </button>
                    </div>
                  </div>
                </fieldset>
              </form>
            </div>
            <div class="social_network_login_block">
              <div class="divider">
                或
              </div>
              <div class="desktop_login">
                <a class="sinaweibo_button" href="javascript:;">
                  <span><img src="./../images/login-icon-weibo.png" alt="">新浪微博</span>
                </a>
                <a class="qq_button" href="javascript:;">
                  <span><img src="./../images/login-icon-qq.png" alt="">QQ登录</span>
                </a>
                <a class="alipay_button" href="javascript:;">
                  <span><img src="./../images/login-icon-alipay.png" alt="">支付宝</span>
                </a>
                <a class="wechat_button" href="javascript:;">
                  <span><img src="./../images/login-icon-wechat.png" alt="">微信</span>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class="header-visibility">
          <div class="shopping" >
            <div class="mini_cart">
              <div class="mini_cart_total">
                <router-link to="/shoppingCart" title="产看购物车" class="mini_cart_link">
                  <span class="mini_cart_label">
                    {{cartCount}}
                  </span>
                </router-link>
              </div>
              <div class="mini_cart_content"></div>
            </div>
          </div>
          <ul class="account_navigation_list">
            <li class="newsletter">
              <a class="newsletter-subscribe" title="订阅电子杂志">订阅电子杂志</a>
            </li>
            <li class="loginout" @click="pulldown">
              <span class="navbar-link" v-text="nickName" v-if="nickName" ></span>
              <a class="loginout-link" title="登录与注册" v-if="!nickName">登录与注册</a>
              <a href="javascript:;" @click="logout" v-else>登出</a>
              <router-link to="/personalCenter" v-if="nickName">个人信息</router-link>
            </li>
          </ul>
          <ul class="utility_navigation_list">
            <li class="country">
              <a class="country-selector" title="中国">中国</a>
            </li>
            <li class="store">
              <a class="store-located" title="专柜查询">专柜查询</a>
            </li>
            <li class="customer">
              <a class="customer-service" title="客服中心">客服中心</a>
            </li>
          </ul>
        </div>
      </div>   
    </div>
    <headerCenter></headerCenter>
    <headerFooter></headerFooter> 
  </header>
</template>

<script>
import axios from 'axios'
import './../assets/css/normalize.css'
import headerCenter from '@/components/navCenter'
import headerFooter from '@/components/navFooter'
export default {
  name: 'header',
  data(){
    return{
      telmail:"",
      password:"",
      isShows:false,
      errTip:false
    }
    
  },
  computed:{
    nickName(){
      return this.$store.state.nickName;
    },
    cartCount(){
      return this.$store.state.cartCount;
    }
  },
  components:{
    headerCenter,
    headerFooter
  },
  mounted(){
    this.checkLogin();
  },
  methods:{
    pulldown(){
      this.isShows = !this.isShows;
    },
    checkLogin(){
      axios.get("/users/checkLogin").then((response)=>{
        let res =response.data;
        var path =this.$route.pathname;
        if(res.status=="0"){
          this.$store.commit("updateUserInfo",res.result);
          this.getCartCount();
        }else{
          // if(this.$route.path!="/"){
          //   this.$router.push("/");
          // }
        }
      });
    },
    login(){
      if(!this.telmail || !this.password){
          this.errTip =true
      }
      axios.post("/users/login",{
        telmail:this.telmail,
        password: this.password
      }).then((response)=>{
        let res =response.data;
        if(res.status=="0"){
          this.isShows=false;
          this.errTip =false;
          // this.nickName =res.result.telmail;
          this.getCartCount();
          this.$store.commit("updateUserInfo",res.result.telmail);
        }
      });
    },
    logout(){
      axios.post("/users/logout").then((response)=>{
        let res=response.data;
        if(res.status="0"){
          this.$store.commit('updateUserInfo','');
        }
      })
    },
    getCartCount(){
      axios.get("/users/getCartCount").then((response)=>{
        let res =response.data;
        this.$store.commit("initCartCount",res.result);
      })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
  width: 996px;
  margin:0 auto;
  padding: 0;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
.header-top{
  top: 0;
  width: 100%;
  background-color: #070707;
  position: relative;
  padding: 14px 0;
  font-family:"SimHei,sans-serif";
  overflow: hidden;
}
.header-top .header-visibility{
  border-bottom: 1px solid #353535;
  padding: 0px 0;
  margin:0 30px;
  height: 20px;

}


/*登录注册*/
.header-hide{
  height: 355px;
}
.account_login_page {
  padding: 54px 0 0;
  position: relative;
}

.account_login_page .account_header_title {
  color: #f3f3f3;
  font-size: 30px;
  font-family: SimHei;
  text-transform: uppercase;
  margin-bottom: 17px;
}

.block_create {
  color: #999;
  font-size: 12px;
  width: 350px;
  float: right;
  padding: 44px 0 10px 43px;
  margin-top: -42px;
  border-left: 1px solid #393939;
}

.block_create .section_header {
  color: #f3f3f3;
  font-family: SimHei;
  font-size: 14px;
  text-transform: uppercase;
  padding: 0 0 20px;
  margin: 0;
}

.block_create p {
  line-height: 1.5;
  width: 307px;
  font-family: SimHei;
  margin: 0 0 10px;
}

.block_create a.button {
  line-height: 26px;
}

.block_create .button {
  margin: 40px 0 0;
}

.button {
  display: inline-block;
  height: 29px;
  padding: 0 22px;
  background: #e6e5e5;
  text-decoration: none;
  font-size: 14px;
  color: #999;
  cursor: pointer;
  border: 1px solid #e6e5e5;
  font-family: SimHei;
}


.block_login {
  color: #999;
  float: left;
  width: 250px;
  padding-bottom: 25px;
}

.block_login fieldset {
  overflow: hidden;
}

fieldset {
    padding: 0;
    border: 0;
    margin: 0;
}

.error_message {
  color: red;
}

.formfield:first-of-type {
  -webkit-margin-top-collapse: separate;
}

.formfield {
  clear: both;
  margin: 10px 0;
}

.login_popup .block_login .formfield .textinput, .login_popup .block_login fieldset {
  width: 250px;
}

.block_login .f_state_error .textinput {
  border: 2px solid #e55757;
}

.f_state_error .textinput {
  padding: 0 9px;
  height: 35px;
}

.textinput, .textarea, .og-popup-container #og_reason_other_text {
  height: 37px;
  border: 1px solid #ccc;
  color: #000;
  padding: 0 10px;
  vertical-align: middle;
  box-sizing: border-box;
  border-radius: 0;
  box-shadow: none;
}

.block_login .formfield_remember_me {
  margin: 0;
  float: left;
}

.block_login .formfield_remember_me label {
  display: inline-block;
}

.block_login .f_label {
  color: #a8a8a8;
}

.formfield label {
  line-height: 24px;
  color: #000;
}

.block_login .formfield_link {
  clear: none;
  float: left;
  line-height: 28px;
  margin: 0;
}

.block_login .formfield_link a {
  float: right;
  position: absolute;
  left: 190px;
  font-size: 12px;
  color: #999;
}

.block_login .formfield_button {
    float: right;
    margin: 10px 0 0;
}

.login_popup .social_network_login_block {
  margin: 5px 0 0 35px;
}

.social_network_login_block {
  border-left: 1px solid #393939;
  float: left;
  margin: 43px 0 0 35px;
  min-height: 97px;
  padding: 5px 0 0 38px;
  position: relative;
  width: 270px;
}
.social_network_login_block .divider {
  position: absolute;
  top: 50%;
  left: 0;
  margin: -17px 0 0 -35px;
  padding: 10px 0;
  width: 70px;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: #070707;
  color: #999;
  text-transform: uppercase;
  text-align: center;
}

.pt_accountloginpopup .desktop_login a {
  color: #c6c6c6;
}

.social_network_login_block a {
  display: block;
  float: left;
  margin-bottom: 10px;
  width: 110px;
}

a {
  text-decoration: none;
  outline: 0;
  color: #c6c6c6;
}
/*购物车*/
.header-top .header-visibility .shopping{
  float: right;
  clear: none;
}

.mini_cart_total{
  line-height: 13px;
  position: relative;
  padding: 13px 12px 16px 54px;
  margin: -14px 0 -15px;
}

.mini_cart_total:before {
  content: "";
  width: 17px;
  height: 20px;
  position: absolute;
  left: 25px;
  top: 11px;
  background: url(/static/images/icons-sprite-25x25.png) no-repeat -375px 0;
}

.mini_cart_link {
  display: block;
  font-size: 12px;
}

.mini_cart_label {
  display: block;
  color: white;
  padding-right: 28px;
  position: relative;
  letter-spacing: .5px;
  text-transform: uppercase;
  font-size: 14px;
}

.mini_cart_label:after {
  content: "";
  width: 5px;
  height: 3px;
  background: url(/static/images/icons-sprite-25x25.png) no-repeat -300px -150px;
  position: absolute;
  right: 0;
  top: 5px;
}



.header-top .header-visibility li a{
  color: #c6c6c6;
  cursor: pointer;
  letter-spacing: .5px;
  text-transform: uppercase;
  display: block;
}
.header-top .header-visibility .account_navigation_list{
  float: right;
  margin-right: -1px;
}

.header-top .header-visibility .account_navigation_list li{
  color: #999;
  float: left;
  font-size: 14px;
  padding: 0 20px;
  border-right: 1px solid #353535;
  line-height: 14px;
  list-style: none;
  position: relative;
}

.header-top .header-visibility .utility_navigation_list{
  float: left;
  max-width: 45%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.header-top .header-visibility .utility_navigation_list:before{ 
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 13px;
  background: #3f3f3f;

}
  
.header-top .header-visibility .utility_navigation_list li{
  float: left;
  font-size: 14px;
  padding: 0 20px 0 20px;
  border-right: 0;
  line-height: 14px;
  position: relative;
}
.loginout span ,.loginout a {
  display: block;
  float: right;
  margin-left: 10px;
}
</style>



