<template>
	<div class="suc">
		<div class="header">
			<div class="container">
				<router-link class="logo" to="/">
					<img src="/static/images/logo.png" alt="">
				</router-link>
				<div class="contact-us">
					<p>24小时免费咨询电话：400-820-6362</p>
					<span>或联系我们的<a href="##">在线客服</a></span>
				</div>
			</div>
		</div>
		<div class="main">
			<div class="container">
				<ul class="order-status">
					<li>1 我的购物袋</li>
					<li>2 填写核对订单</li>
					<li class="active">3 成功提交订单</li>
				</ul>
				<div class="order-suc">
					<p>订单号：{{orderId}}</p>
              		<p>订单总金额：{{orderTotal}}</p>
				</div>
			</div>
		</div>
		<div class="over">
			<div class="container">
				<button>确认付款</button>
			</div>
		</div>
		<myFooter></myFooter>
	</div>
</template>

<script>
	import myFooter from '@/components/myfooter'
	import axios from 'axios'
	export default{
		data(){
            return{
                orderId:'',
                orderTotal:0
            }
        },
		components:{
			myFooter
		},
		mounted(){
			var orderId = this.$route.query.orderId;
            console.log("orderId:"+orderId);
            if(!orderId){
              return;
            }
            axios.get("/users/orderDetail",{
                params:{
                  orderId:orderId
                }
            }).then((response)=>{
                let res = response.data;
                if(res.status=='0'){
                    this.orderId = orderId;
                    this.orderTotal = res.result.orderTotal;
                }
            });
		}
	}
</script>

<style scoped>
	@import url('../assets/css/normalize.css');
    ul li {
    	list-style: none;
    }
	.suc .container {
		width: 996px;
		margin: 0 auto;
	}
	.suc .header {
		width: 100%;
		height: 85px;
		background-color: #070707;
	}
	.suc .header .logo {
		float: left;
		margin-top: 22px;
	}
	.suc .header .contact-us {
		float: right;
		color: #C0C6C6;
	}
	.suc .header .contact-us p {
		margin-top: 15px;
		line-height: 30px;
	}
	.suc .header .contact-us span {
		line-height: 20px;
		float: right;
	}
	.suc .header .contact-us a {
		color: #FFF;
	}
	.suc .main {
		width: 100%;
		overflow: hidden;
	}
	.suc .main .order-status {
		width: 100%;
		overflow: hidden;
	}
	.suc .main .order-status li {
		width: 300px;
		height: 60px;
		line-height: 60px;
		font-size: 16px;
		float: left;
		letter-spacing:2px;
		color: #B1AAAF;
		margin-right: 30px;
		border-bottom: 3px solid #B1AAAF;
	}
	.suc .main .order-status li.active{
		color: #000;
		font-weight: bold;
		border-bottom: 3px solid #000;
	}
	.suc .main .order-suc{
		margin: 0 auto;
		width: 800px;
		border: 1px solid #ccc;
		text-align: center;
		height: 200px;
		line-height: 100px;
	}
	.suc .main .order-suc p{
		font-size: 20px;
	}
	.over {
		width: 500px;
		margin: 0 auto;
		margin-top: 30px;
	}
	button{
		display: block;
		background-color: #d29f56;
		padding: 0 25px 0 38px;
    	height: 28px;
    	line-height: 26px;
    	color: #fff;
    	font-weight: normal;
    	font-family: SimHei;
    	cursor: pointer;
    	border-radius: 0;
    	border: 1px solid #d29f56;
	}
</style>


