<template>
    <div>
        <NavHeader></NavHeader>    
        <div class="userName">
            <ul class="navlist">
                <li><a href="##">首页</a><span></span></li>
                <li><a href="##" v-text="">我的账户</a><span></span></li>
                <li v-text="">
                    
                    <slot name="userName"></slot>
                </li>
            </ul>
            <h1 v-text="userName"></h1>
            <ul class="userList">
                <li v-for="(item,index) in userData">
                    <a href="##" @click="select(item,index)" v-text="item.userName" ></a>
                    <span v-show="item.vertical">|</span>
                    <i v-show="item.isShows"></i>
                </li>                                           
            </ul>
            <user-order v-show="userData[0].isShows"></user-order>
            <user-information v-show="userData[2].isShows" class="userInformation"></user-information>
            <user-address v-show="userData[3].isShows"></user-address>
        </div>     
        <!-- 尾部 -->
        <Service></Service>
        <MyFooter></MyFooter>           
    </div>
</template>
<script>
import axios from 'axios';
import NavHeader from '@/components/navHeader'
import userOrder from "@/components/userOrder";
import userInformation from "@/components/userInformation";
import userAddress from "@/components/userAddress";
import Service from '@/components/service'
import MyFooter from '@/components/myfooter'
export default {
    components:{
        NavHeader,
        userOrder,
        userInformation,
        userAddress,
        Service,
        MyFooter
    },
    data(){
        return{
            userData:[
                {
                userName:"我的订单",
                isShows:true,
                vertical:true                    
            },
                {
                userName:"我的心愿单",
                isShows:false,
                vertical:true                 
            },
                {
                userName:"个人信息",
                isShows:false,
                vertical:true,              
            },
                {
                userName:"我的地址溥",
                isShows:false,
                vertical:true                   
            },
                {
                userName:"我的美丽档案",
                isShows:false,
                vertical:false                  
            }                                                
            ],
            userInformation:'',
            userOrder:"",
            userName:'个人信息'
        }
    },
    methods:{
        select(item,index){
            console.log(item)
            for(var i = 0;i<this.userData.length;i++){
    　　　　this.userData[i].isShows = false    
            }
            this.userData[index].isShows = true;
            this.userName=item.userName     
        }
    }
}	
</script>
<style scoped>
    ul li {
    	list-style: none;
    }
	.userName {
		width: 996px;
		margin: 0 auto;
	}
   .userName .navlist {
   		padding: 0 20px;
    	width: 100%;
    	height: 30px;
    	line-height: 30px;
    }
    .userName .navlist  li  {
    	margin-right: 10px;
        float: left;   
        color: #999;
    }
    .userName .navlist  li a {
    	color: #999;
    	display: block;
    	float: left;
    	line-height: 50px;
    }
    .userName .navlist  li  span {
    	position: relative;
    	top: 6px;
    	display: inline-block;
    	width: 30px;
    	height: 30px;
        background: url(/static/image/icons-sprite-25x25.png) no-repeat -340px -90px;
    }	
    .userName h1 {
    	padding: 0 20px;
    	letter-spacing:2px;
    	font-family: SimHei;
    	margin-bottom: 0;
    }
    .userName .userList {
    	width: 100%;
    	border-bottom: 1px solid #ccc;
    	height: 40px;
    	line-height: 40px;
    	padding: 0 20px;
    }
    .userName .userList li {
    	position: relative;
    	float: left;
    	color: #999;
    	text-align: center;
    }
    .userName .userList li a {
    	color: #666;
    	font-size: 14px;
    	margin-right: 8px;
    }
    .userName .userList li i {
        position: absolute;
    	display: block;
    	width: 10px;
    	height: 10px;
        left: 35%;
        bottom: -5px;        
 		background: url(/static/image/icons-sprite-25x25.png) no-repeat -75px -120px;    
    }
    .userName .userList li span {
    	display: inline-block;
    	margin-right: 8px;
    }
    .userName .userList li a:hover {
    	color: #000;
    }
    .userName .userInformation {

    }
</style>


