<template>
  <div class="header-center">
    <div class="container">
      <div class="logo">
        <router-link to="/" title="YSL">
          <img src="./../images/logo.png" alt="" title="YSL">
        </router-link>   
      </div> 
      <p class="header-font">官方正品保证 官方满400免运费</p>
      <div class="header-search">
        <form action="" method="" class="search" onsubmit="return false">
          <fieldset>
            <label class="search-label" for="">关键字</label>
            <input class="search-input" id="search-input" type="text" placeholder="例如：YSL气垫"  @keyup.enter="sear" v-model="search">
          </fieldset>
        </form>
      </div>
    </div>   
  </div>
</template>

<script>
import './../assets/css/normalize.css'
export default{
  name:"headerCenter",
  data(){
    return{
      search:''
    }
  },
  // computed:{
  //   sear:function(){
  //     var search =this.search;
  //     this.$router.push({path:'/searchPage',query:{title:search}})
  //   }
  // }
  methods:{
    sear(){
      // var search = document.getElementById("search-input").value;
      var search =this.search;
      this.$router.push({path:'/searchPage',query:{title:search}});
      window.location.reload();
    }
  }
}  
</script>

<style>
/*header-center*/
.container{
  width: 996px;
  margin:0 auto;
  padding: 0;
}
.header-center{
  background-color: #070707;
  width: 100%;
  height: 85px; 
  position: relative;
}
.header-center .logo{ 
  width: 250px;
  height: 43px;
  margin-top: -22px;
  margin-left: 30px;
  top: 50%;
  position: absolute;
  display: inline-block;
}

.header-center .header-font{
  color: #999;
  top:40%;
  margin-left: 300px;
  max-width: 320px;
  max-height: 18px;
  overflow: hidden;
  position: absolute;
}

.header-search{
  float: right;
  width: 252px;
  background-color: #fff;
  margin-top: 18px;

}
.search{
    border: 1px solid #c3c3c3;

    text-align: left;
}
.search .search-label{
  display: none;
}
.search .search-input{
  height: 33px;
  display: block;
  font-size: 13px;
  width: 217px;
  padding: 8px 8px 8px 18px;
  outline: 0;
  border: 0;
  background: 0;
}
.search .search-button{
  height: 33px;
  width: 33px;
  text-indent: -9999px;
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background: transparent;
  cursor: pointer;
  z-index: 999;
}
.button{
  display: inline-block;
  margin: 0;
  padding: 0 22px;
  text-decoration: none;
  font-size: 14px;
  color: #999;
  line-height: 27px;
}
fieldset{
  padding: 0;
  margin: 0;
  border: 0;
}
</style>


