<template>
	<div class="header-footer">
		<div class="container">
			<ul class="tab">
				<li v-for="items in list" class="active">
					<router-link :to="items.rtpath">{{items.rtname}}</router-link>
					<div class="xiangqing">
						<h2 v-text="items.name"></h2>
						<h2 v-text="items.xinpin"></h2>

						<ul>
							<li>
								<h2 v-text='items.chunbu'>唇部</h2>
								<p v-for='fenleis in items.fenlei'>{{fenleis}}</p>
								<h2 v-text='items.jiayou'></h2>
							</li>
							<li>
								<h2 v-text='items.mianbu'>面部</h2>
								<p v-for='fenlei1s in items.fenlei1'>{{fenlei1s}}</p>
								<h2 v-text='items.taoshua'>套刷及工具</h2>

							</li>
							<li>
								<h2 v-text='items.yanbu1'>眼部</h2>
								<p v-for='yanbus in items.yanbu'>{{yanbus}}</p>
								
							</li>
							<li>
								<img :src="items.src" alt="">
							</li>
							<div class="tupian">
								<img :src="items.xsrc" alt="">
							</div>
						</ul>
					</div>
				</li>	
			</ul>
		</div>
		<div class="downall">
			<div class="downall-first">
			
			</div>
		</div>
	</div>
</template>

<script>
import './../assets/css/normalize.css'
export default{
	name:"headerFooter",
	data(){
		return{
			list:[
				{
					"rtpath":"lists",
					"rtname":"彩妆",
					"name":"所有彩妆",
					"xinpin":"新品",
					"fenlei":['唇膏','唇釉','唇油','唇蜜','唇颊霜','唇线'],
					"fenlei1":['明彩笔','妆前隔离','粉底','粉饼','蜜粉','腮红'],
					"yanbu":['睫毛膏','眼线','眉笔','眼影'],
					"chunbu":"唇部",
					"mianbu":"面部",
					"yanbu1":"眼部",
					"jiayou":'甲油',
					"taoshua":"套刷及工具",
					"src":"../../static/image/F6599100U_01.jpg"
				},
				{
					"rtpath":"skincare",
					"rtname":"护肤",
					"name":"所有护肤",
					"xinpin":"",
					"fenlei":['隔离防晒','卸妆清洁','爽肤','精华','面霜/乳液','眼唇护理','护肤工具'],
					"fenlei1":['Y型轮廓','全效青春','提高美白','淡斑美白','至臻奢护','基础亮颜'],
					"yanbu":['纯白系列','颜活青春','臻透亮白','藏金奢妍','超模绝密'],
					"chunbu":"按产品分类",
					"mianbu":"按功能分类",
					"yanbu1":"按系列分类",
					"jiayou":'发现更多',
					"src":"../../static/image/q3.jpg"
					
				},
				{
					"rtpath":"purfume",
					"rtname":"香水",
					"name":"香水",
					"xinpin":"",
					"fenlei":['反转巴黎香水','鸦片香水','巴黎香水'],
					"fenlei1":['男士其实香水','自由男士香水','天之骄子男士香水','暗夜男士香水'],
					"yanbu":['黑色之瘾 禁忌香调'],
					"chunbu":"女士香水",
					"mianbu":"男士香水",
					"yanbu1":"发现更多",
					"src":"../../static/image/L7847900U.jpg"
				},
				{
					"rtpath":"star",
					"rtname":"明星产品",
					"name":"热卖产品",
					"xinpin":"新品上市",
					"xsrc":"../../static/image/start_drpdown.jpg"
					
				},
				{
					"rtpath":"giftbox",
					"rtname":"尊享礼盒"
				},
				{
					"rtpath":"official",
					"rtname":"官网礼赠"
				},
				{
					"rtpath":'member',
					"rtname":"会员中心"
				}	
			],
		}
	},
	methods:{
		
	}
}
</script>

<style>
body{
	font-family:"SimHei,sans-serif";
}
.header-footer{
	background-color: #fff;
    width: 100%;
    height: 53px;
}
.header-footer .container{
	width: 100%;
  	margin:0 auto;
  	height: 53px;
}
.tab{
	width: 996px;
	margin: 0 auto;
	padding: 0 30px;
	height: 53px;
}
.tab>li{
	display: inline-block;
	list-style: none;
	float: left;
}
.tab>li a{
	cursor: pointer;
	position: relative;
    display: block;
    padding: 0 33px 4px 33px !important;
    font-size: 18px;
    line-height: 49px;
    color: #070707;
    text-transform: uppercase;
    letter-spacing: .5px;
}

.tab>li a:before{
	content: "";
    display: block;
    width: 5px;
    height: 5px;
    background: url(/static/images/icons-sprite-25x25.png) no-repeat -75px -75px transparent;
    position: absolute;
    top: 50%;
    left: -7px;
    margin-top: -5px;
    font-weight: bold;
}

.tab>li:first-child a:before{
	display: none;
}

.tab li a:hover {
	color: orange;
}
.xiangqing{
	width: 100%;
	height: 400px;
	background-color: white;
	position: absolute;
	z-index: 20;
	left: 0;
	display: none;
	border-top:2px solid #000;
}
.tab li:hover .xiangqing{
	display: block;
}
.tab li .xiangqing>h2{
	margin-left: 100px;
	font-size: 15px;
	line-height: 30px;
}
.tab li .xiangqing ul li{
	float: left;
	width: 150px;
	height: 300px;
	/*border:1px solid red;*/
	margin-left: 100px;
	list-style: none;
	font-size: 13px;
	line-height: 30px;
}
.tab li .xiangqing ul li p{
	font-size: 13px;
}
.tab li .xiangqing ul div{
	width: 300px;
	height: 300px;
	margin-left: 200px;
}
.tab li .xiangqing ul div img{
	position: absolute;
	left: 200px;
}
</style>


