<script>
	import axios from 'axios'
	import myFooter from '@/components/myfooter'
	import {currency} from './../util/currency'
	export default{
		name:'order',
		components:{
			myFooter
		},
		data(){
			return{
              	selectedAddrId:'',
				userName: '',
      			addressName:'',
      			postId: '',
      			tel: '',
				addressLists:[],
				cartLists:[],
				checkIndex:0,
				orderTotal:0,
				subTotal:0
			}
		},
		mounted(){
			this.init();
		},
		computed:{
			totalPrice(){
				var money =0;
				this.cartLists.forEach((items)=>{
					if(items.checked=='1'){
						money+=parseFloat(items.goodsPrice)*parseInt(items.goodsBuyNum);
					}
				})
				return money;
			}
		},
		methods:{
			init(){
				axios.get('/users/addressLists').then((response)=>{
					let res=response.data;
					this.addressLists =res.result;
					this.selectedAddrId = this.addressLists[0].addressId;
				});
				axios.get('/users/cartLists').then((response)=>{
					let res=response.data;
					this.cartLists =res.result;
					this.cartLists.forEach((item)=>{
                    	if(item.checked=='1'){
                        	this.subTotal += item.goodsPrice*item.goodsBuyNum;
                   		}
                	});

                	this.orderTotal = this.subTotal+10;
				})

			},
			addAddress(){
				axios.post('/users/addAddress',{tel: this.tel,postId: this.postId,userName: this.userName,addressName: this.addressName}).then((response)=>{
          			var res = response.data;
          			if(res.status == '0'){
            			//添加成功后
           				console.log(res.msg);
            			// alert("添加成功");
            			this.init();
          			}else{
           
         			}
        		})
			},
			delAddress(addressId){
				axios.post("/users/delAddress",{addressId:addressId}).then((response)=>{
					let res =response.data;
					if(res.status=="0"){
						console.log("del suc");
						this.init();
					}
				})
			},
			setDefault(addressId){
              	axios.post("/users/setDefault",{
                	addressId:addressId
              	}).then((response)=>{
                  	let res = response.data;
                  	if(res.status=='0'){
                     	console.log("set default");
                     	console.log(addressId);
                     	this.init();
                  	}
              	})
          	},
          	payMent(){
              	var addressId = this.$route.query.addressId;
              	axios.post("/users/payMent",{
                	addressId:this.addressLists[0].addressId,
                	orderTotal:this.orderTotal
              	}).then((response)=>{
                 	 let res = response.data;
                  	if(res.status=="0"){
                      	this.$router.push({
                          	path:'/sucOrder?orderId='+res.result.orderId
                      	})
                  	}
              	})
          	}	
		}
	}
</script>

