	}

	.order_summary_product .product_name span{
		color: #666;
	}

	.order_summary_product .product_attributes {
    	display: block;
    	margin-bottom: 12px;
    	margin-left: 65px;
    	color: #999;
	}

	.order_summary_product .product_attributes .attribute {
    	overflow: hidden;
	}

	.order_summary_product .product_total {
    	color: #999;
    	margin-left: 65px;
    	overflow: hidden;
	}

	.secondary .order_summary_product .product_total .label {
    	margin-right: 4px;
	}

	.order_summary_product .label, .order_summary_product .value {
    	float: left;
	}

	.order_summary_product .mini_cart_price, .order_summary_product .bonus_item {
    	color: #000;
    	font-family: SimHei;
    	padding: 0 1px 0 0;
    	width: auto;
    	float: right;
	}

	.mini_cart_price {
    	float: right;
    	color: #000;
   		font-size: 14px;
    	font-weight: bold;
    	display: inline-block;
    	width: 87px;
    	text-align: right;
    	padding-right: 10px;
    	text-transform: uppercase;
	}
	.order .footer .service {
		padding: 50px 0 20px;
		border-top:1px solid #E4E4E4;
		margin: 30px 0;
		width: 1010px;
		overflow: hidden;
	}
    .order .footer .service li {
    	float: left;
    	width: 160px;
    	height: 165px;
    	padding: 0 20px;
    	border-right: 1px solid #ccc;
    	line-height: 20px;
    }
    .order .footer .service li:last-child{
    	border: 0;
    }
    .order .footer .service li p {
    	margin: 20px 0;
    	color: #999;
    }
    .order .footer .service li a {
    	color: #666;
    }
    .order .footer .service li h4 span{
    	display: inline-block;
    	width: 25px;
    	height: 25px;
    	margin-right: 5px;
    	position: relative;
        top: 5px;
    }
    .order .footer .service li:nth-child(1) h4 span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -460px;
    }
    .order .footer .service li:nth-child(2) h4 span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -475px 5px;
    }
    .order .footer .service li:nth-child(3) h4 span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -500px;
    }
    .order .footer .service li:nth-child(4) h4 span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -110px -100px;
    }    
    .order .footer .service li:nth-child(5) h4 span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -540px;
    }            
 	.order .footer .subscribe {
		margin: 20px 0 ;
	}
	.order .footer .subscribe p {
		line-height: 50px;
		font-weight: bold;
	}
	.order .footer .subscribe input{
		height: 28px;
		padding-left: 1em;
	}
	.order .footer .subscribe button{
		height: 30px;
		width: 92px;
		cursor: pointer;
		background-color: #ccc;
		color: #666;
		line-height: 28px;
		text-align: center;
		margin-left: 5px
	}   
</style>


