<template>
	<div class="myFilter">
		<div class="secondary refinements">
			<div class="navigation_panel_wrapper">
				<h1 class="refinement_header">彩妆</h1>
				<ul class="plp_navigation_panel">
					<li>
						<a href="##"  @click="screenAll">所有</a>
					</li>
					<li class="cat_all_makeup all_category" v-for="item in goodsList">
						<a href="##" @click="screen(item)">{{item}}</a>
					</li>
				</ul>
			<div class="refinement_filter_by_box">
				<div class="refinement_filter_by">
					<span @click="toggle()">筛选产品</span>
				</div>
				<div class="filter_section_wrap" v-show="isShow">
					<div class="container">
						<div class="refinement refinement_type">
							<h3 class="refinement_headline toggle refinementhead_cat">
								<span class="hidden">彩妆</span>
								<span class="name">彩妆</span>
							</h3>
							<ul class="refinement_list toggle_content category_level_1">
								<li class="refinement_list_item" v-for="item in goodsList">
									<a href="##" class="refinement_list_link" @click="screen(item)">{{item}}</a>
								</li>
							</ul>
						</div>
						<div class="refinement refinement_color">
							<h3 class="refinement_headline toggle refinementhead_colorfamily">
								<span class="hidden">色彩</span>
								<span class="name">色彩</span>
							</h3>
							<ul class="refinement_list toggle_content swatches scrollable">
								<li class="refinement_list_item  swatch swatch_beiges" v-for="items in goodsBg">
									<a href="##" class="swatch ">
										<span class="swatchBgColor" @click="goodsBgColor(items)" :style="{backgroundColor:items }">

										</span>
									</a>
								</li>
							</ul>
						</div>
						<div class="refinement refinement_price">
							<h3 class="refinement_headline toggle refinementhead_price">
								<span class="hidden">价格范围</span>
								<span class="name">价格范围</span>
							</h3>
							<ul class="refinement_list toggle_content null">
								<li class="refinement_list_item" v-for="range in list">
									<a href="##" class="refinement_list_link" @click="priceFn(range.rangeprice)">{{range.rangeprice}}</a>
								</li>
							</ul>
						</div>
					</div>
				</div>	
				</div>
				<div class="refinement_sort_by">
					<span class="sortby_pc">排序:</span>
					<ul class="refinement_list toggle_content refinement_sortby ">
						<li class="sort_by_list_item refinement_list_item " v-for="ss in sortList">
							<a href="javascript:;" @click="sortGoods(ss.st)">{{ss.st}}</a>
						</li>
					</ul>
				</div>
			</div>
		</div>
		<classification :goodsLists="goodsLists" :isShows="isShows"></classification>
	</div>
</template>


<script>
import axios from 'axios';
import classification from './classification'
export default{
	components:{
			classification
		},
	data(){
		return{
			isShow:false,
			list:[
				{rangeprice:"￥0-￥299"},
				{rangeprice:"￥300-￥499"},
				{rangeprice:"￥500-￥799"}
			],
			sortList:[
				{
					st:"按价格排序(由低到高)"
				},
				{
					st:"按价格排序(由高到低)"
				},
				{
					st:"按销量排序"
				}

			],
			goodsList:[],
			changeColor:[],
			sortFlag: true,
			goodsBg:[],
			goodsLists:[],
			isShows:""
		}
	},
	beforeCreate(){
	  	axios.get('/goods/lists').then((data)=>{
	  		this.goodsLists=data.data;
	  		this.isShows=true;
	  	   //筛选颜色
	  	   var goodsColor=[];
	  	    for(var i=0;i<data.data.length;i++){
	  	    	for(var c=0;c<10;c++){
	  	    		if (data.data[i].goodsBg.color[c]) {
		  	    		goodsColor.push(data.data[i].goodsBg.color[c]);
	  	    		}
	  	    	}
	  	    }
	  	    for(var bg=0;bg<goodsColor.length;bg++){
	  	    	if (this.goodsBg.indexOf(goodsColor[bg])===-1) {
	  	    		this.goodsBg.push(goodsColor[bg])
	  	    	}
	  	    }
	  	},(err)=>{
	  		console.log(err)
	  	});
	},
	mounted(){
		this.getGoodsList();
	},
	methods:{
		//筛选所有
		screenAll(){
		  	axios.get('/goods/lists').then((data)=>{
		  		this.goodsLists=data.data;
		  		this.isShows=true;
	  	   //筛选颜色
	  	   var goodsColor=[];
	  	    for(var i=0;i<data.data.length;i++){
	  	    	for(var c=0;c<10;c++){
	  	    		if (data.data[i].goodsBg.color[c]) {
		  	    		goodsColor.push(data.data[i].goodsBg.color[c]);
	  	    		}
	  	    	}
	  	    }
	  	    for(var bg=0;bg<goodsColor.length;bg++){
	  	    	if (this.goodsBg.indexOf(goodsColor[bg])===-1) {
	  	    		this.goodsBg.push(goodsColor[bg])
	  	    	}
	  	    }
		  	},(err)=>{
		  		console.log(err)
		  	})
		},
		//筛选分类
		screen(item){
			console.log(item)
			this.goodsLists=[];
			axios.get('/goods/lists').then((data)=>{
				for(var i=0;i<data.data.length;i++){
					if (data.data[i].goodsType==item) {
						this.goodsLists.push(data.data[i]);
					}
				}
				this.isShows=false;
		  	   //筛选颜色
		  	   this.goodsBg=[];
		  	   var goodsColor=[];
		  	    for(var g=0;g<this.goodsLists.length;g++){
		  	    	for(var c=0;c<10;c++){
		  	    		if (this.goodsLists[g].goodsBg.color[c]) {
			  	    		goodsColor.push(this.goodsLists[g].goodsBg.color[c]);
		  	    		}
		  	    	}
		  	    }
		  	    for(var bg=0;bg<goodsColor.length;bg++){
		  	    	if (this.goodsBg.indexOf(goodsColor[bg])===-1) {
		  	    		this.goodsBg.push(goodsColor[bg])
		  	    	}
		  	    }				
			},(err)=>{
				console.log(err);
			})
		},
		toggle:function(){
			this.isShow = !this.isShow;
		},
		getGoodsList(){
			var param ={
				sort:this.sortFlag?1:-1
			}
			var _this=this;
			axios.get("/goods",{
				params:param
			}).then((response)=>{
				let res =response.data;
				if(res.status=="0"){
					res.goods.goodsList.forEach((item,index)=>{
						if(_this.goodsList.indexOf(item.goodsType)==-1){
							_this.goodsList.push(item.goodsType);
						}
						item.changeColor.forEach((items,index)=>{
							if(_this.changeColor.indexOf(items.goodsColor)==-1){
								_this.changeColor.push(items.goodsColor);
							}
						})
					});
					//console.log(_this.changeColor);
				}
			})
			// console.log(this.changeColor);
			//console.log(this.goodsList);
		},
		sortGoods:function(value){
			this.sortFlag =!this.sortFlag;
			this.getGoodsList();
			this.isShows=false;
			//按价格低到高排序
			if (value==="按价格排序(由低到高)") {
				var price=[];
				for(var i=0;i<this.goodsLists.length;i++){
					price.push(this.goodsLists[i].goodsPrice)
				}
				price.sort(function(a,b){
					return a-b;
				})
				var goodsSort=[]
				for(var j=0;j<price.length;j++){
					for(var g=0;g<this.goodsLists.length;g++){
						if (this.goodsLists[g].goodsPrice==price[j]) {
							goodsSort.push(this.goodsLists[g])
							this.goodsLists.splice(g,1)
						}
					}
				}
				this.goodsLists=goodsSort;
			};
			//价格高到低排序
			if (value==="按价格排序(由高到低)") {
				var price=[];
				for(var i=0;i<this.goodsLists.length;i++){
					price.push(this.goodsLists[i].goodsPrice)
				}
				price.sort(function(a,b){
					return b-a;
				})
				var goodsSort=[]
				for(var j=0;j<price.length;j++){
					for(var g=0;g<this.goodsLists.length;g++){
						if (this.goodsLists[g].goodsPrice==price[j]) {
							goodsSort.push(this.goodsLists[g])
							this.goodsLists.splice(g,1)
						}
					}
				}
				this.goodsLists=goodsSort;				
			}
		},
		//颜色筛选
		goodsBgColor(items){
			this.isShows=false;
/*	 		for(var g=0;g<this.goodsLists.length;g++){
	 			if (this.goodsLists[g].goodsBg.color.indexOf(items)===-1) {
	 				this.goodsLists.splice(g,1)
	 			}
			}	*/
			this.goodsLists=[];		
			axios.get('/goods/lists').then((data)=>{
				for(var i=0;i<data.data.length;i++){
					if (data.data[i].goodsBg.color.indexOf(items)!==-1) {
						this.goodsLists.push(data.data[i]);
					}
				}
			},(err)=>{

			})	
		},
		//价格筛选
		priceFn(value){
			this.isShows=false;
			var price=value.split("-");
			var priceMin=price[0].slice(1);
			var priceMax=price[1].slice(1);
			console.log(priceMin)
			console.log(priceMax)
			this.goodsLists=[];		
			axios.get('/goods/lists').then((data)=>{
				for(var i=0;i<data.data.length;i++){
					if (data.data[i].goodsPrice>priceMin&&data.data[i].goodsPrice<priceMax) {
						console.log(1111111)
						this.goodsLists.push(data.data[i]);
					}
				}						
			})
		}
	}
}
	
</script>

<style scoped>
ul li{
	list-style: none;
}
.navigation_panel_wrapper{
	text-align: justify;
    margin: 10px 0;
}

.refinement_header{
	display: inline-block;
    font-size: 28px;
    font-family: SimHei;
    text-transform: uppercase;
    line-height: 90%;
}

.plp_navigation_panel{
	background: transparent;
    border: 0;
    display: inline-block;
    float: none;
    text-align: left;
    width: auto;
}
.plp_navigation_panel a{
	font-size: 14px;
    letter-spacing: .25px;
    text-transform: uppercase;
}

.plp_navigation_panel li{
	margin: 0 14px;
	padding: 0;
	position: relative;
	float: left;
}

.plp_navigation_panel li a:after{
	content: "";
    position: absolute;
    left: -17px;
    top: 2px;
    width: 1px;
    height: 11px;
    background: #666;
}

.plp_navigation_panel li:first-child a:after{
	display: none;
}

a{
	text-decoration: none;
	color: #000; 
}

.refinement_filter_by_box{
	overflow: hidden;
	border-top: 1px solid #000;
    border-bottom: 1px solid #000;
}
.refinement_filter_by{
	display: block;
    background: 0;
    font-size: 24px;
    font-family: SimHei;
    text-transform: uppercase;
    text-align: center;
    padding: 12px 0;
    margin: 0 -30px;
    letter-spacing: 1px;
}

.refinement_filter_by span{
	cursor: pointer;
    display: inline-block;
}

.refinement_filter_by span:after{
	content: "";
    display: inline-block;
    margin-left: 17px;
    width: 21px;
    height: 21px;
    vertical-align: top;
    background: url(/static/images/icons-sprite-25x25.png) no-repeat -141px -134px transparent;
}

.filter_section_wrap{
	float: left;
    clear: both;
    width: 100%;
    border-bottom: 1px solid #000;
    border: 1px solid transparent;
}

.filter_section_wrap .container{
	width: auto;
	margin: 0 auto;
	padding: 0;
}

.filter_section_wrap .container .toggle{
	cursor: pointer;
}

.refinement{
	display: inline-block;
    vertical-align: top;
    margin-bottom: 8px;
}

.refinement_headline{
	display: block;
    min-width: 111px;
    max-width: 200px;
    padding: 0 10px;
    margin: 0 0 26px 0;
    font-size: 14px;
    font-weight: normal;
    font-family: SimHei;
    text-transform: uppercase;
    background: 0;
    margin-bottom: 12px;
}

.refinement_color{
	width: 170px;
}

.hidden{
	display: none;
}

.refinement_list_link{
	display: block;
    font-size: 12px;
    text-transform: uppercase;
    font-family: SimHei;
    padding: 2px 25px 3px 10px;
    line-height: 22px;
}

.refinement_sort_by{
	height: 36px;
    display: block;
    margin: 12px -30px 17px;
    padding: 10px 30px 12px;
    background: rgba(0,0,0,0.06);
    line-height: 36px;
}

.sortby_pc,.sort_by_list_item{
	float: left;
	font-weight: bold;
    letter-spacing: .25px;
    text-transform: uppercase;
    font-family: SimHei;
}

.refinement_sort_by .refinement_list_item {
	margin-left: 15px;
	font-size: 14px;
}
/*筛选*/
.swatch_beiges .swatchBgColor{
	display: block;
	width: 28px;
	height: 28px;
	float: left;
	border-radius: 50%;
	margin: 5px;
}
/*.category_level_1 .refinement_list_link:focus {
	
}*/
</style>


