<template>
	<div class="classification">
				<div class="goods-list">
					<ul>
					    <li  v-show="isShows">
							<div class="banner">
							 	<img src="/static/image/MAKEUP_BANNER.jpg" alt="">
							 	<div class="baner-text">
							 		<h1>
							 			采妆 <br>
										MAKEUP
							 		</h1>
							 		<p>拒绝乏味</p>
							 	</div>
							</div>					    	
					    </li>
						<li class="goods" v-for="goods in goodsLists">					   
							<a href="##" class="goods-picture">
							    <p class="promotion" v-text="goods.goodsKind" v-show="goods.goodsKind"></p>
								<p>
									<img :src="goods.goodsImg" alt="">
								</p>
								<h3 class="chinese-title" v-text="goods.goodsEnName"></h3>
								<div class="bgbox">
									<a href="##" @click="modalbox(goods)">
										<span>快速浏览</span>
									</a>
							    </div>
							</a>					
							<h3 class="english-title" v-text="goods.goodsChName"></h3>
							<a href="##" class="sprite" >
								<ul>
									<li v-for="item in stars" :style='{background:item}'></li>
								</ul>
								<span>{{comment}}</span>
							</a> 
							<p class="price" v-text="goods.goodsPrice"></p>
							<button class="purchase" @click="modalbox(goods)"><span></span>立即购买</button>
							<router-link class="routerink" :to="{path:'details',query:{
								goodsId:goods.goodsId
							}}">
								<button class="details" ><span></span>查看详情</button>
							</router-link>
							<div class="buttonBgbox"></div>				
						</li>											
					</ul>
				</div>
			    <modalbox :goodsOne="goodsOne"></modalbox>
	</div>
</template>
<script>
import axios from 'axios';
import modalbox from "./modalbox"
export default {
  name: 'classification',
	data(){
	    return { 		 	
	    	    comment:'0条评论',
				stars:["url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -350px -150px"],	 		 	
	 		 	goodsOne:{}    	
	 		 }	 		 
   },
   props:["goodsLists",'isShows'],
   components:{
    modalbox
  	},
  methods:{
  		modalbox(item){
  			$('.modalbox-bg,.modalbox').show();
  			console.log(item);
  			this.goodsOne=item;
  		}
  }
}	
</script>
<style scoped>
	@import url('../assets/css/normalize.css');
	ul li{
		list-style: none;
	}
	.classification {
		width: 996px;
		margin:40px auto;
		overflow: hidden;
	}
	.classification .banner {
		width: 488px;
		box-sizing: border-box;
	}
	.classification .banner img {
		width: 100%;
		height: 370px;
		display: block;
	}
	.classification .banner .baner-text {
		width: 100%;
		height: 180px;
		text-align: center;
		background-color: #000;
		color: #FFF;
		padding: 10px;
		box-sizing: border-box;
	}
	.classification .banner .baner-text h1 {
		display: inline-block;
		line-height: 35px;
		border-bottom: 1px solid #999;
		padding-bottom: 10px;
	}
    .classification .banner .baner-text p {
		color: #999;
		font-size: 16px;
    }
    .classification .goods-list ul li {
		float: left;
		height: 550px;
    }
    .classification .goods-list ul li:first-child{
    	margin: 5px ;
    }
	.classification .goods-list ul .goods{
		box-sizing: border-box;
		width: 239px;
		border: 1px solid #ccc;
		position: relative;
		margin: 5px;
	}
	.classification .goods-list ul .goods .promotion {
		width: 46px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		position: absolute;
		top: 10px;
		left: 10px;
		background-color: #000;
		color: #FFF;
	}
	.classification .goods-list ul .goods a {
		display: block;
	}
	.classification .goods-list ul .goods .goods-picture {
		padding-top: 40px;
	}
	.recommend .goods-list ul .goods .goods-picture {
		position: relative;
		z-index: 5;
	}
	.classification .goods-list ul .goods a p  {
		width: 190px;
		height: 280px;
		margin: 0 auto;
	}
	.classification .goods-list ul .goods a p img {
		width: 100%;
		height: 100%;
	}
	.classification .goods-list ul .goods h3 {
		text-align: center;
		margin-bottom: 10px;
		font-size: 14px;
		color: #333;
		line-height: 18px;
	}
	/*评价*/
	.classification .goods-list ul .goods .sprite {
		height: 15px;
		margin: 5px 0;
	}
	.classification .goods-list ul .goods .sprite ul {
		width: 80px;
		margin: 20px auto;
	}
	.classification .goods-list ul .goods .sprite ul li {
		border: 0;
		width: 15px;
		height: 15px;
		margin: 0;
	}
	.classification .goods-list ul .goods .sprite span {
		display: none;
	}
	.classification .goods-list ul .goods .price {
		text-align: center;
		margin: 10px 0;
		line-height: 40px;
	}
	.classification .goods-list ul .goods button {
		display: block;
		margin: 5px auto;
		width: 168px;
		height: 28px;
		background-color: #000;
		color: #fff;
		position: relative;
		cursor: pointer;
		border: 1px solid #000;
	}
	.classification .goods-list ul .goods .details {
		background-color: #E6E5E5;
		color: #666;
		border: 1px solid #E6E5E5;
	}
	.classification .goods-list ul .goods button span {
		display: block;
		width: 20px;
		height: 20px;
		position: absolute;
		left: 35px;
		top: 4px;
	}
	.classification .goods-list ul .goods .purchase span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -128px;
	}
	.classification .goods-list ul .goods .details span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -322px;
	}
	.classification .goods-list ul .goods .purchase:hover,
	.classification .goods-list ul .goods .details:hover{
		background-color: #FFF;
		border: 1px solid #666;
		color: #000;
	}
	.classification .goods-list ul .goods .purchase:hover span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -19px -128px;
	}
	.classification .goods-list ul .goods .bgbox {
		width: 100%;
		height: 340px;
		position: absolute;
        top: 0;
        left: 0;
		background-color: rgba(255,255,255,.6);
		display: none;
	}
	.classification .goods-list ul .goods .bgbox a {
		display: block;
		width: 100%;
		height: 338px;
	}
	.classification .goods-list ul .goods .bgbox a span {
		width: 100px;
		line-height: 20px;
		color: #000;
		text-align: center;
		position: absolute;
		top:129px ;
		left: 50%;
		margin-left: -50px;
		border-top: 1px solid #666;
        border-bottom: 1px solid #666;
	}
	.classification .goods-list ul .goods .buttonBgbox {
		width: 100%;
		height: 210px;
		position: absolute;
		left: 0;
		top: 338px;
		background-color: rgba(255,255,255,.6);
		z-index: 6;
		display: none;
	}	    
		/*特效*/
    .classification .goods-list ul .goods .goods-picture:hover .bgbox{
    	display: block;
    }
    .classification .goods-list ul .goods .goods-picture:hover~.buttonBgbox {
    	display: block;
    }		
</style>


