<template>
	<div class="homepageGoodsLists">
		<NavHeader></NavHeader>
	    <Lunbo></Lunbo> 
		<div class="homepage-goodsList">
			<div class="goods-list" v-for='item in classification'>
				<h1 class="title">{{item.activityName}}</h1>
				<!-- 上排商品列表 -->
				<div class="first-goodsList">
					<p class="big-picture">
						<a href="##"><img :src="item.activetyImage.first" ></a>
					</p>
					<div class="small-picture">
						<ul>
							<li v-for="goods in item.topGoodsList">					   
								<a href="##" class="goods-picture">
								    <p class="promotion" v-text="goods.goodsKind" @></p>
									<p>
										<img :src="goods.goodsImg" alt="">
									</p>
									<h3 class="chinese-title" v-text="goods.goodsEnName"></h3>
									<div class="bgbox">
										<a href="##" @click="modalbox(goods)">
											<span>快速浏览</span>
										</a>
								    </div>
								</a>					
								<h3 class="english-title" v-text="goods.goodsChName"></h3>
								<a href="##" class="sprite" v:for="">
									<ul>
										<li v-for="item in item.stars" :style='{background:item}'></li>
									</ul>
									<span>{{item.comment}}</span>
								</a>
								<p class="price" v-text="goods.goodsPrice"></p>	
								<button class="purchase" @click="modalbox(goods)"><span></span>立即购买</button>
								<router-link :to="{path:'details',query:{
									goodsId:goods.goodsId
								}}">
									<button class="/details" ><span></span>查看详情</button>
								</router-link>
								<div class="buttonBgbox"></div>				
							</li>						
						</ul>
					</div>
				</div>
				<!-- 下排商品列表 -->
				<div class="last-goodsList">
					<div class="small-picture">
						<ul>
							<li v-for="goods in item.bottomGoodsList" > 
								<a href="##" class="goods-picture">
								    <p class="promotion" v-text="goods.goodsKind" v-if="goods.goodsKind"></p>
									<p>
										<img :src="goods.goodsImg" alt="">
									</p>
									<h3 class="chinese-title" v-text="goods.goodsEnName"></h3>
									<div class="bgbox">
										<a href="##" @click="modalbox(goods)">
											<span>快速浏览</span>
										</a>
								    </div>
								</a>					
								<h3 class="english-title" v-text="goods.goodsChName"></h3>
								<!-- 评价 -->
								<a href="##" class="sprite">
									<ul>
										<li v-for="item in item.stars" :style='{background:item}'></li>
									</ul>
									<span>{{item.comment}}</span>
								</a>
								<p class="price" v-text="goods.goodsPrice"></p>	
								<button class="purchase" @click="modalbox(goods)"><span></span>立即购买</button>
								<router-link  :to="{path:'/details',query:{
									goodsId:goods.goodsId
								}}">
									<button class="details" ><span></span>查看详情</button>
								</router-link>
								<div class="buttonBgbox"></div>				
							</li>	
						</ul>
					</div>
					<p class="big-picture">
						<a href="##"><img :src="item.activetyImage.last" alt="" ></a>
					</p>
				</div>			
			</div>
			<modalbox :goodsOne="goodsOne"></modalbox>
		</div>
		<modalbox></modalbox>
    	<Service></Service>
    	<MyFooter></MyFooter>
	</div>
</template>
<script>
import axios from 'axios'
import NavHeader from '@/components/navHeader'
import Lunbo from '@/components/lunbo'
import modalbox from '@/components/modalbox'
import Service from '@/components/service'
import MyFooter from '@/components/myfooter'
export default {
  	name: 'homepageGoodsLists',
	data(){
	    return {
	    	classification:[
	    	],
	    	goodsOne:null
	    }
   	},
   	components:{
  		NavHeader,
    	Lunbo,
   	 	modalbox,
    	Service,
    	MyFooter
  	},

  	methods:{
  		modalbox(item){
  			$('.modalbox-bg,.modalbox').show(); 	
  			this.goodsOne=item;	
  		}
  	},
	mounted(){
		this.goodsOne={};
		//请求分类活动数据
		axios.get('/activitys/activity')
		.then((data)=>{
			console.log(data.data)
			for(var i=0;i<data.data.length;i++){
				this.classification.push(data.data[i]);	               
			}		
			console.log(this.classification[0].activetyImage.first)	
		},(err)=>{
			console.log(err)
			
		});
				
		//请求商品数据
		axios.get('/goods/lists')
		.then((data)=>{
			//console.log(data)
			var goods=6;
			for(var i=0;i<this.classification.length;i++){
				for(var g=0+i*goods;g<3+i*goods;g++){
					this.classification[i].topGoodsList.push(data.data[g])
				};
				for(g=3+i*goods;g<6+i*goods;g++){
					this.classification[i].bottomGoodsList.push(data.data[g])
				}			
			}
		},(err)=>{
			console.log(err)
		});		
	},
	destroyed(){
		console.log();
	}		
} 
</script>
<style scoped>
	@import url('../assets/css/normalize.css');
    ul li {
    	list-style: none;
    }
	.homepage-goodsList {
		padding:0 2%;
		min-width:996px;
		margin: 10px auto;
		font-size: 14px;
		font-family: "Segoe UI Light";
	}
	.homepage-goodsList .title{
		text-align: center;
		font-size: 80px;
		line-height: 80px;
		letter-spacing:0.1em;
	}
	.homepage-goodsList .first-goodsList {
		min-width:996px;
		overflow: hidden;
		margin-top: 10px;
	}
	.homepage-goodsList .last-goodsList {
		min-width:996px;
		overflow: hidden;
		margin-top: 10px;
	}
	.homepage-goodsList .big-picture{
		box-sizing: border-box;
		width: 39.4%;
		height: 450px;
		border: 1px solid #ccc;
		float: left;
	}
	.homepage-goodsList .small-picture ul li{
		box-sizing: border-box;
		width: 19.4%;
		height: 450px;
		border: 1px solid #ccc;
		float: left;
		position: relative;
	}
	.homepage-goodsList .small-picture ul li,
	.homepage-goodsList  .big-picture {
		margin-left: 0.6%;
	}
	.homepage-goodsList  .big-picture img {
		width: 100%;
		height: 100%;
	}
	.homepage-goodsList .small-picture ul li .promotion {
		width: 46px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		position: absolute;
		top: 10px;
		left: 10px;
		background-color: #000;
		color: #FFF;
	}
	.homepage-goodsList .small-picture ul li a {
		display: block;
	}
	.homepage-goodsList .small-picture ul li .goods-picture {
		position: relative;
		z-index: 5;
	}
	.homepage-goodsList .small-picture ul li a p  {
		width: 190px;
		height: 260px;
		margin: 0 auto;
	}
	.homepage-goodsList .small-picture ul li a p img {
		width: 100%;
		height: 100%;
	}
	.homepage-goodsList .small-picture ul li h3 {
		text-align: center;
		margin-bottom: 10px;
		font-size: 14px;
		color: #333;
		letter-spacing: .25px;
		line-height: 18px;
	}
	/*评价*/
	.homepage-goodsList .small-picture ul li .sprite {
		height: 15px;
		margin: 5px 0;
	}
	.homepage-goodsList .small-picture ul li .sprite ul {
		width: 80px;
		margin: auto;
	}
	.homepage-goodsList .small-picture ul li .sprite ul li {
		border: 0;
		width: 15px;
		height: 15px;
		/*background: url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px;*/
	}
	.homepage-goodsList .small-picture ul li .sprite ul li:last-child {
		/*background: url(/static/image/icons-sprite-25x25.png) no-repeat -350px -150px;*/
	}
	.homepage-goodsList .small-picture ul li .sprite span {
		display: none;
	}
	.homepage-goodsList .small-picture ul li .price {
		text-align: center;
		margin: 10px 0;
	}
	.homepage-goodsList .small-picture ul li button {
		display: block;
		margin: 5px auto;
		width: 168px;
		height: 28px;
		background-color: #000;
		color: #fff;
		position: relative;
		cursor: pointer;
		border: 1px solid #000;
	}
	.homepage-goodsList .small-picture ul li .details {
		background-color: #E6E5E5;
		color: #666;
		border: 1px solid #E6E5E5;
	}
	.homepage-goodsList .small-picture ul li button span {
		display: block;
		width: 20px;
		height: 20px;
		position: absolute;
		left: 35px;
		top: 4px;
	}
	.homepage-goodsList .small-picture ul li .purchase span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -128px;
	}
	.homepage-goodsList .small-picture ul li .details span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -322px;
	}
	.homepage-goodsList .small-picture ul li .purchase:hover,
	.homepage-goodsList .small-picture ul li .details:hover{
		background-color: #FFF;
		border: 1px solid #666;
		color: #000;
	}
	.homepage-goodsList .small-picture ul li .purchase:hover span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -19px -128px;
	}
	.homepage-goodsList .small-picture ul li .bgbox {
		width: 100%;
		height: 280px;
		position: absolute;
        top: 0;
        left: 0;
		background-color: rgba(255,255,255,.6);
		display: none;
	}
	.homepage-goodsList .small-picture ul li .bgbox a {
		display: block;
		width: 100%;
		height: 278px;
	}
	.homepage-goodsList .small-picture ul li .bgbox a span {
		width: 100px;
		line-height: 20px;
		color: #000;
		text-align: center;
		position: absolute;
		top:129px ;
		left: 50%;
		margin-left: -50px;
		border-top: 1px solid #666;
        border-bottom: 1px solid #666;
	}
	.homepage-goodsList .small-picture ul li .buttonBgbox {
		width: 100%;
		height: 172px;
		position: absolute;
		left: 0;
		top: 278px;
		background-color: rgba(255,255,255,.6);
		z-index: 6;
		display: none;
	}
	/*特效*/
    .homepage-goodsList .small-picture ul li .goods-picture:hover .bgbox{
    	display: block;
    }
    .homepage-goodsList .small-picture ul li .goods-picture:hover~.buttonBgbox {
    	display: block;
    }	
</style>


