<template>
	<div>
		<div class="recommend" v-for='item in recommend'>
		    <div class="line">
		    	<p></p>
		    </div>
			<h3 class="tetle" v-text="item.tetle"></h3>
			<div class="goods-list">
				<ul>
					<li v-for="goods in item.goodsList">					   
						<a href="##" class="goods-picture">
						    <p class="promotion" v-text="goods.goodsKind" v-show="goods.goodsKind"></p>
							<p>
								<img :src="goods.goodsImg" alt="">
							</p>
							<h3 class="chinese-title" v-text="goods.goodsEnName"></h3>
							<div class="bgbox">
								<a href="##" @click="modalbox(goods)">
									<span>快速浏览</span>
								</a>
						    </div>
						</a>					
						<h3 class="english-title" v-text="goods.goodsChName"></h3>
						<a href="##" class="sprite">
							<ul>
								<li v-for="item in item.stars" :style='{background:item}'></li>
							</ul>
							<span>{{item.comment}}</span>
						</a>
						<p class="price" v-text="goods.goodsPrice"></p>
						<button class="purchase" @click="modalbox(goods)"><span></span>立即购买</button>
						<router-link to='/details'>
							<button class="details" ><span></span>查看详情</button>
						</router-link>
						<div class="buttonBgbox"></div>				
					</li>											
				</ul>
			</div>
		</div>		
		<modalbox :goodsOne="goodsOne"></modalbox>
	</div>
</template>
<script>
import axios from 'axios';
import modalbox from '@/components/modalbox'
export default {
  name: 'homepageGoodsList',
  	components:{
  		modalbox
  	},
	data(){
	    return {
	 		recommend:[
	 		 {
	 		 	tetle:"你可能还喜欢",
			    line:"url(/static/images/icons-sprite-25x25.png) no-repeat -375px -50px",	 		 	
				comment:'0条评论',
			 	stars:["url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -350px -150px"],	 		 	
	 		 	goodsList:[
	 		 	  	 		 	  	 		 	  
	 		 	]
	 		 },
	 		 {
	 		 	tetle:"日常搭配必备",
	 		 	line:"url(/static/images/icons-sprite-25x25.png) no-repeat -375px -50px",
				comment:'0条评论',
			 	stars:["url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -325px -150px",
			 	"url(/static/image/icons-sprite-25x25.png) no-repeat -350px -150px"],	 		 	
	 		 	goodsList:[
 		 	  
	 		 	]
	 		 }	 		 
	 		],
	 		goodsOne:{}	 
	    }
   },
  methods:{
  		modalbox(item){
  			$('.modalbox-bg,.modalbox').show();
  			this.goodsOne=item
  		}
  },
  	created(){
  		var id=this.$route.query.goodsId;
  		axios.get("/goods/goodsOne",{params:this.$route.query}).then((data)=>{
  			var goodsType=data.data.goodsType
	  		axios.get('/goods/lists').then((data)=>{
	  			var goodsUnique=[];
				for(var g=0;g<data.data.length;g++){
					//猜你喜欢
					if (data.data[g].goodsId!==id&&goodsType===data.data[g].goodsType&&this.recommend[0].goodsList.length<4) {
						this.recommend[0].goodsList.push(data.data[g]);
					}
					//日常搭配必备
					if (goodsType!=data.data[g].goodsType&&this.recommend[1].goodsList.length<4) {
						//console.log(data.data[g].goodsType);
						if(goodsUnique.indexOf(data.data[g].goodsType) == -1){
								goodsUnique.push(data.data[g].goodsType);
								if (this.recommend[1].goodsList.indexOf(goodsUnique)) {
									//console.log(data.data[g].goodsType)
									this.recommend[1].goodsList.push(data.data[g]);
								}
						}
					}
				}					
	  		});  			
  		})
	}
}
</script>
<style scoped>
	@import url('../assets/css/normalize.css');
	ul li {
		list-style: none;
	}
	.recommend {
		width: 996px;
		margin: 0 auto;
		overflow: hidden;
	}
	.recommend .line {
		width: 100%;
		margin-top: 60px;
		position: relative;
		border-top: 1px solid #999;
	}
	.recommend .line p {
	    position: absolute;
	    left: 50%;
	    top: 50%;
	    transform: translate(-50%,-50%);
		width: 140px;
		height: 13px;
		background: url(/static/images/icons-sprite-25x25.png) no-repeat -375px -50px;  
	}
	.recommend .tetle {
		text-align: center;
		line-height: 60px;
		margin: 20px 0;
	}
	.recommend .goods-list ul li{
		box-sizing: border-box;
		width: 239px;
		height: 450px;
		border: 1px solid #ccc;
		float: left;
		position: relative;
		margin-right: 10px;
	}
	.recommend .goods-list ul li .promotion {
		width: 46px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		position: absolute;
		top: 10px;
		left: 10px;
		background-color: #000;
		color: #FFF;
	}
	.recommend .goods-list ul li a {
		display: block;
	}
	.recommend .goods-list ul li .goods-picture {
		position: relative;
		z-index: 5;
	}
	.recommend .goods-list ul li a p  {
		width: 190px;
		height: 260px;
		margin: 0 auto;
	}
	.recommend .goods-list ul li a p img {
		width: 100%;
		height: 100%;
	}
	.recommend .goods-list ul li h3 {
		text-align: center;
		margin-bottom: 10px;
		font-size: 14px;
		color: #333;
		letter-spacing: .25px;
		line-height: 18px;
	}
	/*评价*/
	.recommend .goods-list ul li .sprite {
		height: 15px;
		margin: 5px 0;
	}
	.recommend .goods-list ul li .sprite ul {
		width: 80px;
		margin: 0 auto;
	}
	.recommend .goods-list ul li .sprite ul li {
		border: 0;
		width: 15px;
		height: 15px;
		margin: 0;
	}
	.recommend .goods-list ul li .sprite span {
		display: none;
	}
	.recommend .goods-list ul li .price {
		text-align: center;
		margin: 10px 0;
	}
	.recommend .goods-list ul li button {
		display: block;
		margin: 5px auto;
		width: 168px;
		height: 28px;
		background-color: #000;
		color: #fff;
		position: relative;
		cursor: pointer;
		border: 1px solid #000;
	}
	.recommend .goods-list ul li .details {
		background-color: #E6E5E5;
		color: #666;
		border: 1px solid #E6E5E5;
	}
	.recommend .goods-list ul li button span {
		display: block;
		width: 20px;
		height: 20px;
		position: absolute;
		left: 35px;
		top: 4px;
	}
	.recommend .goods-list ul li .purchase span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -128px;
	}
	.recommend .goods-list ul li .details span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat 0px -322px;
	}
	.recommend .goods-list ul li .purchase:hover,
	.recommend .goods-list ul li .details:hover{
		background-color: #FFF;
		border: 1px solid #666;
		color: #000;
	}
	.recommend .goods-list ul li .purchase:hover span{
		background: url(/static/image/icons-sprite-25x25.png) no-repeat -19px -128px;
	}
	.recommend .goods-list ul li .bgbox {
		width: 100%;
		height: 280px;
		position: absolute;
        top: 0;
        left: 0;
		background-color: rgba(255,255,255,.6);
		display: none;
	}
	.recommend .goods-list ul li .bgbox a {
		display: block;
		width: 100%;
		height: 278px;
	}
	.recommend .goods-list ul li .bgbox a span {
		width: 100px;
		line-height: 20px;
		color: #000;
		text-align: center;
		position: absolute;
		top:129px ;
		left: 50%;
		margin-left: -50px;
		border-top: 1px solid #666;
        border-bottom: 1px solid #666;
	}
	.recommend .goods-list ul li .buttonBgbox {
		width: 100%;
		height: 172px;
		position: absolute;
		left: 0;
		top: 278px;
		background-color: rgba(255,255,255,.6);
		z-index: 6;
		display: none;
	}	
		/*特效*/
    .recommend .goods-list ul li .goods-picture:hover .bgbox{
    	display: block;
    }
    .recommend .goods-list ul li .goods-picture:hover~.buttonBgbox {
    	display: block;
    }	
</style>


