<template>
	<div class="userAddress">
      <div class="left">
          <div class="Address">
              <h3>我的收货地址</h3>
              <p class="null" v-show="!addressLists[0]">没有保存的地址，为了节省您的支付时间，可在这里添加您常用的收货地址。</p>
              <p class="append"><a  href="##" @click="appendAddress">添加新地址</a></p>
              <div class="appendAddress"  v-show="isShow">
                  <p>
                      姓名：<input type="text" v-model="userName">
                  </p>   
                  <p>
                      邮编：<input type="text" v-model="postId"  @blur="MyPost">
                  </p>                    
                  <span v-show="isPost">请输入有效的邮政编码</span>
                  <p>
                      地址：<input type="text" v-model="addressName">
                  </p>  
                  <p>
                      电话：<input type="text" v-model="tel" @blur="myTel">
                  </p>  
                  <span v-show="isTel">请输入有效的手机号码</span>
                  <p>
                      <a href="##" @click="cancel">取消</a>
                      <a href="##" @click='MyAddress'>确定并保存</a>
                  </p>                                            
              </div>    
              <div class="user-address"  v-for="(item,index) in addressLists" v-show="addressLists[0]">
                  <p><input type="radio" name="aaa"><span>默认地址</span></p>
                  <p>姓名:{{item.userName}}</p>
                  <p>邮编:{{item.postId}}</p>
                  <p>地址:{{item.addressName}}</p>
                  <p>手机号码:{{item.tel}}</p>
                  <p>
                    <a href="##" @click="modifyRess(item,index)">修改</a>
                    <a href="##" @click="removeRess(item,index)">删除</a>
                  </p>
                  <div class="appendAddress"  v-show="item.checked">
                      <p>
                          姓名：<input type="text" v-model="item.userName">
                      </p>   
                      <p>
                          邮编：<input type="text" v-model="item.postId" >
                      </p>                    
                      <p>
                          地址：<input type="text" v-model="item.addressName">
                      </p>  
                      <p>
                          电话：<input type="text" v-model="item.tel" >
                      </p>  
                      <p>
                          <a href="##" @click="modifyCancel(item,index)">取消</a>
                          <a href="##" @click='modifyMyAddress(item,index)'>确定并保存</a>
                      </p>                                            
                  </div>   
              </div>       
          </div>
          <div class="shop">
              <h3>我收藏的实体店</h3>
              <p class="null">您没有收藏的实体店，请添加新实体店。</p>
              <a class="append" href="##">添加新实体店铺</a>                       
          </div>
      </div>
      <div class="right">
          <h3>我们能帮您什么?</h3>
          <p class="help" v-for="item in helps">
            <span></span> <a href="##" v-text="item"></a>
          </p>
          <p class="line"></p>
          <div class="service">
              <h3>客户服务</h3>
              <p>
                <span></span>400-820-6362
              </p>
              <p>（周一至周日 9:00-21:00）</p>
              <p>联系我们</p>
          </div>
      </div>
	</div>
</template>
<script>
import axios from 'axios';
export default {
    data(){
      return {
        helps:["如何下订单?","如何查询订单配送状况?","如何修改和取消订单?","是否可以退掉已经订购的产品?","有哪些付款方式可以选择?","有哪些送货方式?","怎样才能获得产品的建议?"],
        isShow:false,
        isPost:"",
        isTel:"",
        addressName:"",
        userName:"",
        tel:"",
        postId:"",
        addressLists:[]
      }
    },
    methods:{
        appendAddress(){
          this.isShow=true;
        },
        cancel(){
           this.isShow=false;
        },
        MyPost(){
           var reg=/\d{6}/
           if (reg.test(this.postId)) {
              this.isPost=false;
           }else{
              this.isPost=true;
           }
        },
        myTel(){
            //手机正则判断
            var treg =  /^1([358][0-9]|[4][57]|[7][0135678])\d{8}$/;
            if (treg.test(this.tel)) {
               this.isTel=false;
            }else{
                this.isTel=true;
            }
         },
         modifyCancel(item,index){
            item.checked=false;
         },
         //修改
         modifyRess(item,index){
            for(var i=0;i<this.addressLists.length;i++){
              this.addressLists[i].checked=false;
            }
             item.checked=true;     
         },
        modifyMyAddress(item,index){
            axios.post('/users/modifyRess',{userName:item.userName,postId:item.postId,addressName:item.addressName,tel:item.tel,index}).then((response)=>{
                        var res = response.data;
                        if(res.status == '0'){

                        } 
                        item.checked=false;          
            })  
         },
         //删除
        removeRess(item,index){
            axios.post('/users/removeRess',{index}).then((reponse)=>{
              this.addressLists=[];
              axios.get('/users/addressLists').then((response)=>{
                                          var res = response.data;
                                          if(res.status == '0'){
                                            for(var i=0;i<res.result.length;i++){
                                              this.addressLists.push(res.result[i])
                                            }
                                          }           
                              }) 
            })
        },
        MyAddress(){
             //console.log(this.userName)
            axios.post('/users/addAddress',{userName:this.userName,addressName:this.addressName,tel:this.tel,postId:this.postId}).then((response)=>{
                    var res = response.data;
                    if(res.status == '0'){
                        //添加成功后
                        //console.log(res);
                        alert("添加成功");
                        this.isShow=false;
                        this.addressLists=[];
                        axios.get('/users/addressLists').then((response)=>{
                                    var res = response.data;
                                    if(res.status == '0'){
                                      for(var i=0;i<res.result.length;i++){
                                        this.addressLists.push(res.result[i])
                                      }
                                    }           
                        })                       
                    }else{
                         alert("添加失败");
                    }
            })
        }
    },
    mounted(){
        axios.get('/users/addressLists').then((response)=>{
                    var res = response.data;
                    if(res.status == '0'){
                      for(var i=0;i<res.result.length;i++){
                        this.addressLists.push(res.result[i])
                      }
                    }           
        })
     }
}	
</script>
<style scoped>
    ul li {
        list-style: none;
    }
    .userAddress {
        font-size: 14px;
        padding: 0 20px;
        overflow: hidden;
    }  
    .userAddress>div {
        margin-top: 20px;
    }
    .userAddress .left {
        width: 700px;
        padding-right: 30px;
        float: left;
    }
    .userAddress .left h3 {
        font-size: 14px;
        line-height: 20px;
        padding: 10px 0;
        border-bottom: 3px solid #000;
    }
    .userAddress .left .null {
        color: #666;
        padding: 20px 0;
        line-height: 20px;
    }
    .userAddress .left .append  {
       margin-top: 20px;
       overflow: hidden;
    }
    .userAddress .left .append a{
       display: block;
       width: 120px;
       height: 30px;
       line-height: 30px;
       background-color: #000;
       color: #FFF;
       border: 1px solid #000;
       text-align: center;
       float: right;
    }
    .userAddress .left>div {
       overflow: hidden;
    }
    .userAddress .left .append a:hover{
      background-color: #FFF;
      color: #000;
    }
    .userAddress .left .Address .appendAddress p{
      line-height: 30px;
      margin: 10px 0;
       overflow: hidden;
    }
    .userAddress .left .Address .appendAddress span {
      font-size: 12px;
      color: #E56C66;
    }
    .userAddress .left .Address .appendAddress p input {
      width: 300px;
      height: 28px;
    }
    .userAddress .left .Address .appendAddress p a {
      display: block;
      width: 100px;
      height: 30px;
      line-height: 30px;
      background-color: #000;
      color: #FFF;
      border: 1px solid #000;
      text-align: center;
      float: right;
      margin-left: 20px;
    }
    .userAddress .left .Address .appendAddress p a:hover {
      background-color: #FFF;
      color: #000;
    }
    .userAddress .left .Address .user-address p:first-child{
      margin: 10px 0;
    }
    .userAddress .left .Address .user-address p+p{
      padding-left: 15px;
      line-height: 20px;
    }
    .userAddress .left .Address .user-address p:last-child{
      margin: 20px 0;
    }
    .userAddress .left .Address .user-address p:last-child a {
      color: #888;
      margin-right: 20px;
    }
    .userAddress .right{
      float: left;
      width: 200px;
      padding-left: 25px;
      border-left: 1px solid #ccc;
    }
    .userAddress .right h3 {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 20px;
    }
    .userAddress .right .help {
      line-height: 20px;
      margin: 10px 0;
    }
    .userAddress .right .help span{
      position: relative;
      display: inline-block;
      width: 10px;
      height: 8px;
      background: url(/static/image/icons-sprite-25x25.png) no-repeat -350px -125px;
    }
     .userAddress .right .help a {
        color: #888;
     }
     .userAddress .right .line {
        margin: 30px auto;
        height: 20px;
        width: 110px;
        background: url(/static/image/icons-sprite-25x25.png) no-repeat -375px -50px;
     }
      .userAddress .right .service p {
        line-height: 20px;
      }
      .userAddress .right .service p:nth-of-type(1),
       .userAddress .right .service p:nth-of-type(3){
        color: #000;
        font-weight: bold;
      }
      .userAddress .right .service p:nth-of-type(2){
        color: #888
      }
     .userAddress .right .service p span{
        display: inline-block;
        position: relative;
        top: 5px;
        width: 30px;
        height: 20px;
        background: url(/static/image/icons-sprite-25x25.png) no-repeat -475px 0;
     }
     .userAddress .left .Address .user-address .appendAddress p a {
        color: #FFF;
     }
     .userAddress .left .Address .user-address .appendAddress p a:hover {
        color: #000;
     }
     .userAddress .left .Address .user-address .appendAddress p{
        padding: 0;
     }
</style>


