<template>
	<div class="userInformation">
       <h4>个人信息</h4>
       <ul v-if="userOrder">
           <li>
                性别：&emsp;<input type="radio" name="sex">&emsp;女士&emsp;
                <input type="radio" name="sex">&emsp;男士
           </li>
           <li>
               姓名：<br>
               <input type="text"  v-model="userOrder.userInfoList.userNames" ><br>
           </li>
           <li>
               生日：<br>
               <input type="date" >
           </li>     
           <li>
               手机：<br>
               <input type="text"  v-model="userOrder.userInfoList.tel" @blur="checktel"><br>
                <span v-show="isTel">请输入有效的手机电话</span>
           </li>   
           <li>
               邮箱：<br>
               <input type="text" v-model="userOrder.userInfoList.mail" @blur="checkmail"><br>
               <span v-show="isMail">请输入正确的邮箱</span>
           </li>                
       </ul>
       <p>
           <input type="checkbox" name="confirm"> <span class="subscribe">我愿意订阅圣罗兰电子杂志。</span>
       </p>
       <p>
           我已阅读和了解圣罗兰官网的<a href="##">使用条款</a>和<a href="##">隐私声明</a>，并且同意接受使用条款。
       </p>
       <div class="modify">
           <button @click="modefyUser">保存我的修改</button>
           <a href="##" @click="modifyPassword">修改密码</a>
       </div>
       <div class="modify-password" v-show="isShow">
            <h3>重新设置新密码</h3>
           <p>
               &emsp;旧密码：<input type="password" v-model="oldPassword" @blur="oldPasswordF" >
           </p>
           <span v-show="isOldPassword">密码有误</span>
           <p>
               &emsp;新密码：<input type="password" v-model="newPassword" @blur="newPasswordF">
           </p>
           <span v-show="isNewPassword">格式有误</span>
           <p>
               确定密码：<input type="password" v-model="confirmPassword" @blur="confirmPasswordF">
           </p>
           <span v-show="isConfirmPassword">与新密码不一致</span>
           <p>
               <a href="##" @click="updatePassword">提交</a>
               <a href="##" @click="cancelPassword">取消</a>
           </p>
       </div>
       <div class="bgColor"></div>
	</div>
</template>
<script>
import axios from 'axios';
export default {
    data(){
        return {
            isShow:false,
            userOrder:null,
            oldPassword:'',
            newPassword:"",
            confirmPassword:"",
            isTel:"",
            isMail:'',
            isOldPassword:"",
            isNewPassword:"",
            isConfirmPassword:""
        }
    },
    methods:{
         modifyPassword(){
            this.isShow=true
         },
         cancelPassword(){
            this.isShow=false
         },
         //手机正则判断
         checktel(){
            var treg =  /^1([358][0-9]|[4][57]|[7][0135678])\d{8}$/;
            if (treg.test(this.userOrder.userInfoList.tel)) {
               this.isTel=false;
            }else{
                this.isTel=true;
            }
         },
         //邮箱正则判断
         checkmail(){
           var ereg =/^[\w-]+@[0-9a-zA-Z]+(\.[a-z]+){1,3}$/;
            if (ereg.test(this.userOrder.userInfoList.mail)) {
               this.isMail=false;
            }else{
                this.isMail=true;
            }
         },
         //密码正则判断
         oldPasswordF(){
            var reg = /^.{6,10}$/;
            if (reg.test(this.oldPassword)) {
                console.log(11111)
                this.isOldPassword=false;
            }else{
                this.isOldPassword=true;
            }
         },
         newPasswordF(){
            var reg = /^.{6,10}$/;
            if (reg.test(this.newPassword)) {
                this.isNewPassword=false;
            }else{
                this.isNewPassword=true;
            }
         },
         confirmPasswordF(){
            if (this.newPassword===this.confirmPassword) {
                this.isConfirmPassword=false;
            }else{
                this.isConfirmPassword=true;
            }
         },
         //个人信息
         modefyUser(){
            axios.post("/users/userInfo",{userNames:this.userOrder.userInfoList.userNames,tel:this.userOrder.userInfoList.tel,mail:this.userOrder.userInfoList.mail}).then((response)=>{
                    var res = response.data;
                    if(res.status == '0'){
                        //添加成功后
                       // console.log(res);
                         alert("成功");
                    }else{
           
                    }
            })
         },
         //重置密码
         updatePassword(){
            axios.post('/users/updatePassword',{oldPassword:this.oldPassword,newPassword:this.newPassword,confirmPassword:this.confirmPassword}).then((response)=>{
                    var res = response.data;
                    if(res.status == '0'){
                        //添加成功后
                        //console.log(res);
                        alert("修改成功");
                    }else{
                         alert("修改失败");
                    }
            })
         }
    },
    mounted(){
        //请求用户数据
        var userInformation={
           userNames:"",
           tel:"",
           mail:""
        }
        axios.get('/users/userList').then((response)=>{
                    var res = response.data;
                    if(res.status == '0'){
                        if (res.result.userInfoList&&res.result.userInfoList.userNames&&res.result.userInfoList.tel&&res.result.userInfoList.mail) {
                            this.userOrder=res.result;
                        }else{
                            this.userOrder=res.result;
                            this.userOrder.userInfoList=userInformation;
                          /*  this.userOrder.userInfoList.userNames='';
                            this.userOrder.userInfoList.tel='';
                            this.userOrder.userInfoList.mail='';  */                           
                        }
                    }           
        })
    }    
}	
</script>
<style scoped>
    ul li {
        list-style: none;
    }
    .userInformation {
        font-size: 14px;
        padding: 0 20px;
    }
    .userInformation h4 {
        line-height: 50px;
        border-bottom: 5px solid #000;
    }
    .userInformation ul {
        margin-top: 20px;
        overflow: hidden;
    }
    .userInformation ul li {
        margin: 10px 0;
        line-height: 24px;
    }    
    .userInformation ul li+li input,
    .userInformation .modify-password p input{
        padding-left: 10px;
        width: 300px;
        height: 38px;
    }
    .userInformation ul li span,
    .userInformation .modify-password span {
        color: #E66C6C;
        font-size: 12px
    }
    .userInformation p .subscribe{
        font-weight: bold;
    }
    .userInformation .modify {
        margin-top: 60px;
    }
    .userInformation .modify button {
        width: 166px;
        height: 30px;
        line-height: 30px;
        background-color: #000;
        color: #FFF;
        margin-right: 20px;
        cursor: pointer;
    }
    .userInformation .modify button:hover {
        box-shadow:0px 0px 1px 1px #000;
    }
    .userInformation .modify a {
        color: #666;
        font-size: 16px;
    }
    .userInformation .modify-password{
        width: 450px;
        overflow: hidden;
        margin: 20px 0;
    }
    .userInformation .modify-password h3 {
        text-align: center;
        margin: 20px 0;
    }
    .userInformation .modify-password span {
        display: block;
        line-height: 30px;
        text-align: center;
    }
    .userInformation .modify-password  p{
        margin: 10px 0;
    }
     .userInformation .modify-password  p a {
        display: inline-block;
        width: 100px;
        height: 30px;
        line-height: 30px;
        background-color: #000;
        color: #FFF;
        border: 1px solid #000;
        text-align: center;
        margin-right: 30px;
     }
     .userInformation .modify-password  p a:hover {
        color: #000;
        background-color: #fff;
     }

</style>


